import { Component, ViewChild } from "@angular/core";
import { DISTRICTService } from "../../../services/district.services";
import { GlobalAssets } from "../../../environments/Global";
import { CommonService } from "../../../services/common.service";
import { AccessToken } from "../../../environments/access_token";
import { CommonMessageComponent } from "../../../environments/CommanMssage";
import { Router } from "@angular/router";
import { LangulageConf } from "../../../environments/language.config";
import { DistrictShgMasterModel } from "./shg.model.component";
import { NgForm } from "@angular/forms";
import { Title } from "../../../../../node_modules/@angular/platform-browser";
import { TitleConstants } from "../../../utils/constants/title-constants";
import { DataTableDirective } from "../../../../../node_modules/angular-datatables";
import { Subject } from "../../../../../node_modules/rxjs";




@Component({
    templateUrl : '../shg/shg.component.html',
    styleUrls: ['../../../components/app.css']
})

export class DistrictSHGComponent{
    public Cmrcs: any;
    public schemes: any;
    public showErrorMessage: any;
    public savingGroups: any = "";
    public showWarningMessage: boolean;
    public message: any;
    public Vos: any;
    public showMessage: any;
    public areas: any;
    public villagess: any;
    public NotAllowDelete: boolean = false;
    public AllowDelete: boolean = false;
    public deleteDisable: boolean = true;
    public fd: string;
    public deleterecord: any;
    @ViewChild(DataTableDirective)
    dtElement: DataTableDirective;
    dtInstance: DataTables.Api;
    public dtTrigger: Subject<any> = new Subject();
    constructor(public _districtService: DISTRICTService,
        public _globalURL: GlobalAssets,
        public _commonService: CommonService,
        public _access_token: AccessToken,
        public _commonMessage: CommonMessageComponent,
        public _router: Router,
        public lang: LangulageConf,
        public shgUpdateModel: DistrictShgMasterModel,
        public titleService: Title
    ){
        this.setTitle(TitleConstants.DISTRICT_SHG_DETAILS);
    }


    ngOnInit() {
    //    this.getMembers();
        this.getCMRCDashboardKVP();
        this.getSchemeKVP();
        this.getAreaKVP();
        this.getVillageKVP();
    }

    ngAfterViewInit(): void {
        this.dtTrigger.next();
    }

    public setTitle(newTitle: string) {
        this.titleService.setTitle(newTitle);
    }
    getCMRCDashboardKVP(): void {
        debugger;
        this._globalURL.isLoading = true;
        this._districtService.get(this._globalURL.GET_KVP_DISTRICT_CMRC_DASHBOARD, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.Cmrcs = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this._globalURL.isLoading = false;
                }
            });
    }
    
    getVillageKVP(): void {
        debugger;
        this._globalURL.isLoading = true;
        this._districtService.get(this._globalURL.KVPGET_VILLAGE, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._commonMessage.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this._router.navigate(['../login']);
                }
                if (res.Status == true) {
                    this.villagess = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._commonMessage.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                }
            }).catch((err) => {
                this._globalURL.isLoading = false;
                this.message = this._commonMessage.CatchBlockMsg;
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            });
    }


    getAreaKVP(): void {
        this._globalURL.isLoading = true;
        this._districtService.get(this._globalURL.GET_AREA_KVP, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._commonMessage.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this._router.navigate(['../login']);
                }
                if (res.Status == true) {
                    this.areas = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._commonMessage.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                }
            }).catch((err) => {
                this._globalURL.isLoading = false;
                this.message = this._commonMessage.CatchBlockMsg;
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            });
    }


    getSchemeKVP(): void {
        this._globalURL.isLoading = true;
        this._districtService.get(this._globalURL.GET_SCHEME_KVP, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._commonMessage.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this._router.navigate(['../login']);
                }
                if (res.Status == true) {
                    this.schemes = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._commonMessage.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                }
            }).catch((err) => {
                this._globalURL.isLoading = false;
                this.message = this._commonMessage.CatchBlockMsg;
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            });
    }



    getVoByCmrcId(selectCmrc:any): void{
        debugger;
        this._globalURL.isLoading = true;
        this._districtService.get((this._globalURL.GET_VO_BY_CMRC_ID + "?cmrcId=" + selectCmrc),sessionStorage.getItem("access_token"))
        .then((res) => {
            if(res.Status == true){
                this.Vos = res.Data;
                this._globalURL.isLoading = false;
            }
            else{
                this._globalURL.isLoading = false;
            }
        })
    }

    public latestVoId : number = 0;
    getShgByVoIdAndStatus(selectedVoId: any): void {
        debugger;
        this.latestVoId = selectedVoId;
        this._globalURL.isLoading = true;
        this._districtService.get(this._globalURL.GET_ALL_SHG_BYVOIDSTATUS + "?voId=" + selectedVoId + "&status=" + 2 , sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._commonMessage.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this._router.navigate(['../login']);
                }
                if (res.Status == true) {
                    this.savingGroups = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._commonMessage.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                }
                this.reRenderDT();
            })
            .catch((err) => {
                this._globalURL.isLoading = false;
                this.message = this._commonMessage.CatchBlockMsg;
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            })
    }

    setUpdatedValues(data: any) {    
        this.shgUpdateModel = data;
    }

    updateSHG() {
        debugger;
     this._districtService.post(this._globalURL.UPDATE_SHG_MASTER,this.shgUpdateModel, sessionStorage.getItem("access_token"))
         .then((res) => {
             if (res.status === 401) {
                 sessionStorage.clear();
                 this.showErrorMessage = true;
                 this.message = this._commonMessage.error401message;
                 setInterval(() => {
                     this.showErrorMessage = false;
                 }, 5000);
                 this._globalURL.isLoading = false;
                 this._router.navigate(['../login']);
             }
             if (res.Status == undefined) {
                 var data = JSON.parse(res._body);
                 if (data.Status == true) {
                    // updatedData.control.reset();
                     this._globalURL.isLoading = false;
                     this.getShgByVoIdAndStatus(this.latestVoId);
                     this.showMessage = true;
                     this.message = this._commonMessage.SHGUpdateMsg;
                     setInterval(() => {
                         this.showMessage = false;
                     }, 5000);
                    // this.getMembers();
                 }
                 else {
                     this._globalURL.isLoading = false;
                     this.getShgByVoIdAndStatus(this.latestVoId);
                     this.message = this._commonMessage.WarningMsg;
                     this.showErrorMessage = true;
                     setInterval(() => {
                         this.showErrorMessage = false;
                     }, 5000);
                 }
             }
             else {
                 if (res.Status == true) {
                     this._globalURL.isLoading = false;
                     this.showMessage = true;
                     this.message = this._commonMessage.SHGUpdateMsg;
                     setInterval(() => {
                         this.showMessage = false;
                     }, 5000);
                     //this.getMembers();
                 }
                 else{
                     this.showWarningMessage = true;
                     this.message = this._commonMessage.WarningMsg;
                     setInterval(() => {
                         this.showWarningMessage = false;
                     }, 5000);
                     this._globalURL.isLoading = false;
                 }
             }
         })
         .catch((err) => {
             this._globalURL.isLoading = false;
             this.message = this._commonMessage.CatchBlockMsg;
             this.showErrorMessage = true;
             setInterval(() => {
                 this.showErrorMessage = false;
             }, 5000);
         })
 }
  reRenderDT() {
        this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
            dtInstance.destroy();
            this.dtTrigger.next();
        });
    } 

    fordelete(data: any) {
        debugger;
        this.AllowDelete = false;
        this.NotAllowDelete = false;
        this.deleterecord = data;
        this._districtService.get((this._globalURL.IF_CAN_DELETE_SHG + data.Id + "&ForId=1"), sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this._globalURL.isLoading = false;
                    for (var item in res.Data) {
                        if (res.Data[item].Key == 0) {
                            this.NotAllowDelete = false;
                            this.AllowDelete = true;
                        }
                        else {
                            this.AllowDelete = false;
                            this.NotAllowDelete = true;
                            this.fd = res.Data[item].Value;
                        }
                    }
                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._commonMessage.WarningMsg;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                }
            }).catch((res) => {
                this.showErrorMessage = true;
                this.message = this._commonMessage.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this._globalURL.isLoading = false;
            })
    }


    deleteShg(id: number) {
        debugger;
        this._districtService.post((this._globalURL.DELETE_SHG + id), null, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 200) {
                    this._globalURL.isLoading = false;
                    this.message = this._commonMessage.SHGDeleteMsg;
                    this.showMessage = true;
                    setInterval(() => {
                        this.showMessage = false;
                    }, 5000);
                    this.getShgByVoIdAndStatus(this.latestVoId);
                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._commonMessage.WarningMsg;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                }
            }).catch((res) => {
                this.showErrorMessage = true;
                this.message = this._commonMessage.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this._globalURL.isLoading = false;
            })
    }
}


