import { Component, ViewChild } from "@angular/core";
import { GlobalAssets } from "../../../environments/Global";
import { LangulageConf } from '../../../environments/language.config';
import { DISTRICTService } from "../../../services/district.services";
import { AccessToken } from "../../../environments/access_token";
import { Router } from "@angular/router";
import { CommonService } from "../../../services/common.service";
import { GlobalVariable } from "../../../environments/GlobalVeriables";
import { FundMasterModel } from "./fund.model.component";
import { DatepickerOptions } from 'ng2-datepicker';
import * as frLocale from 'date-fns/locale/en';
import { CommonMessageComponent } from "../../../environments/CommanMssage";
import { NgForm } from "@angular/forms";
import { DataTableDirective } from "../../../../../node_modules/angular-datatables";
import { Subject } from "../../../../../node_modules/rxjs";
import { Title } from "../../../../../node_modules/@angular/platform-browser";
import { TitleConstants } from "../../../utils/constants/title-constants";


@Component({
    templateUrl: '../fund/fund.component.html',
    styleUrls: ['../../../components/app.css']
})

export class DistrictFundComponent {
    public convertedWordsInEnglish: string;
    public convertedWordsInMarathi: string;
    public convertedWordsInHindi: string;
    public Vos: any;
    public Cmrcs: any;
    public fundDetail: any;
    public showType1: boolean = true;
    public showType2: boolean = true;
    public updateapprove: boolean;
    public fundTypeId: number;
    public cmrcId: any;
    public voId: number;
    public fundModel: any;
    public message: string;
    public showMessage: boolean;
    @ViewChild(DataTableDirective)
    dtElement: DataTableDirective;
    dtInstance: DataTables.Api;
    public dtTrigger: Subject<any> = new Subject();
    public ApprovedDate: Date = new Date();
    options: DatepickerOptions = {
        minYear: 2000,
        maxYear: (new Date().getFullYear() + 1),
        displayFormat: 'MMM D[,] YYYY',
        barTitleFormat: 'MMMM YYYY',
        dayNamesFormat: 'dd',
        firstCalendarDay: 0, // 0 - Sunday, 1 - Monday
        locale: frLocale,
        //minDate: new Date(Date.now()), // Minimal selectable date
        //minDate: new Date("2016-03-03"),
        //maxDate: new Date(Date.now()),  // Maximal selectable date
        barTitleIfEmpty: 'Click to select a date'
    };
    showErrorMessage: boolean;
    showWarningMessage: boolean;
    fundid: any;
    setcmrcId: any;

    constructor(public districtService: DISTRICTService,
        public _globalURL: GlobalAssets,
        public _access_Token: AccessToken,
        public _router: Router,
        public lang: LangulageConf,
        public _commonService: CommonService,
        public _commonMessage: CommonMessageComponent,
        public _globalVariable: GlobalVariable,
        public fundData: FundMasterModel,
        public titleService: Title
    ) {
        this.fundModel = null;
        this.fundTypeId = 1;
        if (sessionStorage.getItem("forbacktofund") == "true"){
            sessionStorage.setItem("forbacktofund",null);
            this.getFund(sessionStorage.getItem("setvoid"), 2);
            this.cmrcId = sessionStorage.getItem("cmrcIdforFund");
            this.voId = parseInt(sessionStorage.getItem("setvoid"));
        }
        this.setTitle(TitleConstants.DISTRICT_FUND_DETAILS);
        if (sessionStorage.getItem("access_token") == "")
            _router.navigate(['../login']);
    }

    ngOnInit() {
        this.getCMRCsKVP();
    }
    public setTitle(newTitle: string) {
        this.titleService.setTitle(newTitle);
    }
    ngAfterViewInit(): void {
        this.dtTrigger.next();
    }
    public selectedVOId: number = 0;
    public selectedStatus: number = 0;

    forUpdate(data: any) {
        this.convertToWords(data.ApproveAmount);
        this.fundData = data;
    }
    fordelete(data: any) {
        this.fundid = data.Id;
    }
    deleteRecord() {
        this.districtService.post((this._globalURL.DELETE_FUND + this.fundid), null, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status == 200) {
                    this._globalURL.isLoading = false;
                    this.showMessage = true;
                    this.message = this._commonMessage.Deletefundmsg;
                    setInterval(() => {
                        this.showMessage = false;
                    }, 5000);

                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._commonMessage.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                }
            }).catch((res) => {
                this.showErrorMessage = true;
                this.message = this._commonMessage.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this._globalURL.isLoading = false;
            })
    }
    forGetFund(id: number) {
        this.getFund(this.selectedVOId, id);
    }
    getFund(voId: any, status: number) {
        this._globalURL.isLoading = true;
        this.selectedVOId = voId;
        sessionStorage.setItem("setvoid", voId);
        this.selectedStatus = status;
        this.fundTypeId = status;
        if (status == 1) {
            this.showType1 = true;
        }
        if (status == 2) {
            this.showType2 = true;
            this.showType1 = false;
        }
        this.fundModel = null;
        this.districtService.get((this._globalURL.GET_FUND_BY_VOID + "?status=" + status + "&voId=" + voId), sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.fundModel = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._commonMessage.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                }
            }).catch((res) => {
                this.showErrorMessage = true;
                this.message = this._commonMessage.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this._globalURL.isLoading = false;
            })
    }

    getVoByCmrcId(selectCmrc: any): void {
        debugger;
        sessionStorage.setItem("cmrcIdforFund", selectCmrc);
        this._globalURL.isLoading = true;
        this.setcmrcId = selectCmrc;
        this.districtService.get((this._globalURL.GET_VO_BY_CMRC_ID + "?cmrcId=" + selectCmrc), sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.Vos = res.Data; debugger;
                    this._globalURL.isLoading = false;
                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._commonMessage.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                }
            }).catch((res) => {
                this.showErrorMessage = true;
                this.message = this._commonMessage.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this._globalURL.isLoading = false;
            })
    }

    getCMRCsKVP(): void {
        debugger;
        this._globalURL.isLoading = true;
        this.districtService.get(this._globalURL.GET_KVP_DISTRICT_CMRC_DASHBOARD, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.Cmrcs = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._commonMessage.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                }
            }).catch((res) => {
                this.showErrorMessage = true;
                this.message = this._commonMessage.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this._globalURL.isLoading = false;
            })
    }


    public setUpdate(fundDetail: any) {
        debugger;
        this.fundData = fundDetail;
        if (this.fundTypeId == 1) {
            this.showType1 = true;
            this.showType2 = false;
        }
        if (this.fundTypeId == 2) {
            this.showType2 = true;
            this.showType1 = false;
        }

    }

    navigateToFundRepayment(id: number) {
        this._globalVariable.SelectedFundId = id;
        this._router.navigate(['/district/UpdateFundRepayment']);
    }
    public confirmUpdate(Data: any) {
        this._globalURL.isLoading = true;debugger;
        this.districtService.post(this._globalURL.UPDATE_DISTRICT_FUND_DETAILS, Data, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 200) {
                    Data.control.reset()
                    this._globalURL.isLoading = false;
                    this.getFund(this.selectedVOId, this.selectedStatus);
                    this.message = this._commonMessage.UpdateMsg;
                    this.showMessage = true;
                    setInterval(() => {
                        this.showMessage = false;
                    }, 5000);
                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._commonMessage.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                }
            }).catch((res) => {
                this.showErrorMessage = true;
                this.message = this._commonMessage.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this._globalURL.isLoading = false;
            })
    }

    reRenderDT() {
        this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
            dtInstance.destroy();
            this.dtTrigger.next();
        });
    }

    convertToWords(num) {
        debugger;
        this.convertedWordsInEnglish = this._commonService.convertDigitToWordsInEnglish(num);
        this.convertedWordsInHindi = this._commonService.convertDigitToWordsInHindi(num);
        this.convertedWordsInMarathi = this._commonService.convertDigitToWordsInMarathi(num);
    }
}


