import {Component, OnInit} from '@angular/core';
import { CMRCService } from '../../../../services/cmrc.services';
import { Router } from '@angular/router';
import { AccessToken } from '../../../../environments/access_token';
import { GlobalVariable } from '../../../../environments/GlobalVeriables';
import { GlobalAssets } from '../../../../environments/Global';
import { ExportToPDF } from '../../../../services/reports/exportpdf';
import { LangulageConf } from '../../../../environments/language.config';

@Component ({
    templateUrl: 'memberreport.html',
})

export class MembeReports implements OnInit{
    Members: any;
    shgs: any;
    Vos: any;
    message: string;
    ShowErrormsg: boolean;
    constructor(public _cmrcservice: CMRCService,
        public _router: Router,
        public lang: LangulageConf,
        public _access_token: AccessToken,
        public globalVariable: GlobalVariable,
        public _globalURL: GlobalAssets,
        public exportToPDF: ExportToPDF){
    }
    ngOnInit(){
        this.getVoIdAndName();
    }

      //GET vo in dropdown ...
      getVoIdAndName(): void {
        this._globalURL.isLoading = true;
        debugger;
        this._cmrcservice.get(this._globalURL.GET_VO_CMRCID, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.Vos = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this.ShowErrormsg = true;
                    this.message = "Vo is not get";
                    setInterval(() => {
                        this._globalURL.isSaveSuccessMessage = false;
                        this.ShowErrormsg = false;
                    }, 5000);
                }
            });
    }
    
      //get shg dropdown..
      getShgNameAndAndMembers(voID: any): void {
        //  let id = data.voId;
        this._globalURL.isLoading = true;
        debugger;
        // let finalUrl = this._globalURL.GET_SHGNAME_ID_VOID + " " + id
        this._cmrcservice.get((this._globalURL.GET_SHGNAME_ID_VOID + "" + voID), sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.shgs = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this._globalURL.isLoading = false;
                }
            });
    }

    getMembersbyShgId(SHGId: any): void {
        //  let id = data.voId;
        debugger;
        this._globalURL.isLoading = true;
        debugger;
        // let finalUrl = this._globalURL.GET_SHGNAME_ID_VOID + " " + id
        this._cmrcservice.get((this._globalURL.GET_ALL_MEMBER_DETAILS_BY_SHGID + "" + SHGId + "&Status=" + 1), sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.Members = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this._globalURL.isLoading = false;
                }
            });
    }

    memberPdf(){
        debugger;
        this._globalURL.isLoading = true;
        this.exportToPDF.memberExportToPdf(this.Members);
        this._globalURL.isLoading = false;
    }
}


