import { Component, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { LangulageConf } from '../../../environments/language.config';
import { AccessToken } from '../../../environments/access_token';
import { GlobalAssets } from '../../../environments/Global';
import { CMRCService } from '../../../services/cmrc.services';
import { FileUploadService } from '../../../services/FileService/file_upload.service';
import { CommonService } from '../../../services/common.service';
import { GlobalVariable } from '../../../environments/GlobalVeriables';
import { CommonMessageComponent } from '../../../environments/CommanMssage';
import { DatepickerOptions } from 'ng2-datepicker';
import * as frLocale from 'date-fns/locale/en';
import { NgForm } from '@angular/forms';
import { Title } from '../../../../../node_modules/@angular/platform-browser';
import { TitleConstants } from '../../../utils/constants/title-constants';
import { DataTableDirective } from 'angular-datatables';
import { Subject } from 'rxjs';

@Component({
    templateUrl: '../fund/fund.component.html',
    styleUrls: ['../../../components/app.css']
})

export class CMRCFundComponent implements OnInit {
    @ViewChild(DataTableDirective)
    dtElement: DataTableDirective;
    dtInstance: DataTables.Api;
    public dtTrigger: Subject<any> = new Subject();
    public changePhotoDetails: any;
    public showMessage: boolean;
    public showWarningMessage: boolean;
    public message: string;
    public showErrorMessage: boolean;
    public fundgroups: any;
    public showType2: boolean = false;
    public showType1: boolean = true;
    public fundTypeId: number;
    public fundDetails: any;
    public totalRequestAmt: any;
    public totalApprovedAmt: any;
    public viewUpdateFundValues: any;
    public getAllFundTypes: any;
    public voKVPs: any;
    public getDataBySelectedVoId: number;
    public convertedWordsInEnglishApp: string;
    public convertedWordsInMarathiApp: string;
    public convertedWordsInHindiApp: string;
    public convertedWordsInEnglishReq: string;
    public convertedWordsInMarathiReq: string;
    public convertedWordsInHindiReq: string;
    public RequestAmount: number = 0;
    public ApproveAmount: number = 0;
    public requestamt: any;
    public ApprovedDate: Date = new Date;
    options: DatepickerOptions = {
        minYear: 2000,
        maxYear: (new Date().getFullYear() + 1),
        displayFormat: 'MMM D[,] YYYY',
        barTitleFormat: 'MMMM YYYY',
        dayNamesFormat: 'dd',
        firstCalendarDay: 0, // 0 - Sunday, 1 - Monday
        locale: frLocale,
        barTitleIfEmpty: 'Click to select a date'
    };
    constructor(public _cmrcService: CMRCService,
        public _globalURL: GlobalAssets,
        public _access_token: AccessToken,
        public _router: Router,
        public lang: LangulageConf,
        private _fileUploadService: FileUploadService,
        public _commonService: CommonService,
        public globalVariables: GlobalVariable,
        public _common_message: CommonMessageComponent,
        public titleService: Title
    ) {
        if (sessionStorage.getItem("access_token") == "") {
            _router.navigate(['../login']);
            this.convertedWordsInEnglishReq = "";
            this.convertedWordsInMarathiReq = "";
            this.convertedWordsInHindiReq = "";
        }
        this.fundDetails = null;
        this.totalRequestAmt = null;
        this.totalApprovedAmt = null;
        this.viewUpdateFundValues = null;
        this.getAllFundTypes = null;
        this.fundTypeId = this.globalVariables.NEW_FUND;
        this.getDataBySelectedVoId = 0;
        this.setTitle(TitleConstants.CMRC_FUND_DETAILS);
    }

    ngOnInit(): void {
        this.getFund(this.fundTypeId, 0);
    }

    ngAfterViewInit(): void {
        this.dtTrigger.next();
    }

    public setTitle(newTitle: string) {
        this.titleService.setTitle(newTitle);
    }

    getFund(status: number, voId?: number | number): void {
        this.fundTypeId = status;
        this.fundDetails = null;
        this._globalURL.isLoading = true;
        this.totalRequestAmt = 0;
        this.totalApprovedAmt = 0;
        this.getDataBySelectedVoId = voId;
        this._cmrcService.get((this._globalURL.GET_FUND_DETAILS_BY_VO_ID + this.fundTypeId), sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    for (var item in res.Data) {
                        this.totalRequestAmt = this.totalRequestAmt + res.Data[item].RequestAmount;
                        this.totalApprovedAmt = this.totalApprovedAmt + res.Data[item].ApproveAmount;
                    }
                    this.fundDetails = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._common_message.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                        this._globalURL.isLoading = false;
                    }, 5000);
                }
                this.reRenderDT();
            }).catch((res) => {
                this.message = this._common_message.CatchBlockMsg;
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                    this._globalURL.isLoading = false;
                }, 5000);
            })
    }
    
    UpdateLoanVIewDetails(data: any) {
        this.convertToWordsReq(data.RequestAmount)
        this.viewUpdateFundValues = data;
        this.requestamt = this.viewUpdateFundValues.RequestAmount;
    }

    UpdateFundRequest(data1: NgForm, frDt: Date, metadata: any) {
        let updateData = {
            'ToVOId': this.viewUpdateFundValues.ToVOId, 'ToVOName': this.viewUpdateFundValues.ToVOName,
            'RequestAmount': this.viewUpdateFundValues.RequestAmount, 'InterestRate': data1.value.InterestRate,
            'Tenure': data1.value.tenure, 'FundTypeId': data1.value.FundTypeId,
            'ApproveAmount': data1.value.ApproveAmount, 'Status': 2, 'Id': this.viewUpdateFundValues.Id,
            'PaymentMode': data1.value.PaymentMode,
            "ApprovedDate": data1.value.ApprovedDate
        }

        this._globalURL.isLoading = true;
        this._cmrcService.post(this._globalURL.UPDATE_FUND_FROM_CMRC_TO_VO, updateData, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this._router.navigate(['../login']);
                }
                if (res.Status == undefined) {
                    if (res.status === 200) {
                        this._globalURL.isLoading = false;
                        this.getFund(this.globalVariables.NEW_FUND);
                        this.showMessage = true;
                        this.message = this._common_message.FundApproveMsg;
                        setInterval(() => {
                            this.showMessage = false;
                        }, 5000);
                        //send Bank receipt

                        var bankFile = metadata.form[5].files[0];
                        var bankFileFormData = new FormData();
                        bankFileFormData.append("file", bankFile);
                        bankFileFormData.append("RefId", this.viewUpdateFundValues.Id);
                        this._fileUploadService.post((this._globalURL.ADD_PHOTO + "?folderName=BankReceipt" + "&type=" + this._globalURL.CMRC_TO_VO_FUND_BANK_RECEIPT + "&RefId=" + this.viewUpdateFundValues.Id + "&PhotoId=0"), bankFileFormData, sessionStorage.getItem("access_token"))
                            .then((res) => {
                                if (res.Status == undefined) {
                                    if (res.status == 200) {
                                        this._globalURL.isLoading = false;
                                        this.showMessage = true;
                                        this.message = this._common_message.PhotoUploadMsg;
                                        setInterval(() => {
                                            this.showMessage = false;
                                        }, 5000);
                                        data1.control.reset();
                                    }
                                    else {
                                        this.showWarningMessage = true;
                                        this.message = this._common_message.WarningMsg;
                                        setInterval(() => {
                                            this.showWarningMessage = false;
                                        }, 5000);
                                        this._globalURL.isLoading = false;
                                    }
                                }
                            }).catch((res) => {
                                this._globalURL.isLoading = false;
                                this.message = this._common_message.CatchBlockMsg;
                                this.showErrorMessage = true;
                                setInterval(() => {
                                    this.showErrorMessage = false;
                                }, 5000);
                            })

                        var checkFile = metadata.form[6].files[0];
                        var checkFileFormData = new FormData();
                        checkFileFormData.append("file", checkFile);
                        checkFileFormData.append("RefId", this.viewUpdateFundValues.Id);
                        this._fileUploadService.post((this._globalURL.ADD_PHOTO + "?folderName=BankReceipt" + "&type=" + this._globalURL.CMRC_TO_VO_FUND_CHECK_RECEIPT + "&RefId=" + this.viewUpdateFundValues.Id + "&PhotoId=0"), checkFileFormData, sessionStorage.getItem("access_token"))
                            .then((res) => {
                                if (res.Status == undefined) {
                                    if (res.status == 200) {
                                        this._globalURL.isLoading = false;
                                        this.showMessage = true;
                                        this.message = this._common_message.PhotoUploadMsg;
                                        setInterval(() => {
                                            this.showMessage = false;
                                        }, 5000);
                                        data1.control.reset();
                                    }
                                    else {
                                        this.showWarningMessage = true;
                                        this.message = this._common_message.ErrorPhotoUploadMsg;
                                        setInterval(() => {
                                            this.showWarningMessage = false;
                                        }, 5000);
                                        this._globalURL.isLoading = false;
                                    }
                                }
                            }).catch((res) => {
                                this._globalURL.isLoading = false;
                                this.message = this._common_message.CatchBlockMsg;
                                this.showErrorMessage = true;
                                setInterval(() => {
                                    this.showErrorMessage = false;
                                }, 5000);
                            })
                    }
                    else {
                        this._globalURL.isLoading = false;
                        this.message = this._common_message.ErrorFundupdateMsg;
                        this.showErrorMessage = true;
                        setInterval(() => {
                            this.showErrorMessage = false;
                        }, 5000);
                    }
                }
                else {
                    if (res.Status == true) {
                        this._globalURL.isLoading = false;
                        this.showMessage = true;
                        this.message = this._common_message.FundUpdateMsg;
                        setInterval(() => {
                            this.showMessage = false;
                        }, 5000);
                        data1.control.reset();
                        this.getFund(this.globalVariables.NEW_FUND);
                    }
                    else {
                        this._globalURL.isLoading = false;
                        this.message = this._common_message.ErrorFundupdateMsg;
                        this.showErrorMessage = true;
                        setInterval(() => {
                            this.showErrorMessage = false;
                        }, 5000);
                    }
                }
            }).catch((res) => {
                this._globalURL.isLoading = false;
                this.message = this._common_message.CatchBlockMsg;
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            })
    }

    showRepayment(fundId: number) {
        debugger;
        this.globalVariables.SelectedFundId = fundId;
        this._router.navigate(['/cmrc/fundRepayment']);
    }

    convertToWordsReq(num) {
        this.convertedWordsInEnglishReq = this._commonService.convertDigitToWordsInEnglish(num);
        this.convertedWordsInHindiReq = this._commonService.convertDigitToWordsInHindi(num);
        this.convertedWordsInMarathiReq = this._commonService.convertDigitToWordsInMarathi(num);
    }

    forchange(data: any) {
        this.changePhotoDetails = data;
    }

    public chnageBankPhoto(metadata: any, refId: string, phtoId?: number) {
        var formData = new FormData();
        formData.append("RefId", refId);
        formData.append("file", metadata);
        this._fileUploadService.post((this._globalURL.ADD_PHOTO + "?folderName=BankReceipt" + "&type=" + this._globalURL.CMRC_TO_VO_FUND_BANK_RECEIPT + "&RefId=" + refId + "&PhotoId=" + (phtoId == 0 || phtoId == null ? 0 : phtoId)),
            formData, sessionStorage.getItem("access_token")).then((res) => {
                if (res.Status == undefined) {
                    if (res.status == 200) {
                        this.getFund(this.globalVariables.APPROVED_FUND, 0)
                        this.showMessage = true;
                        this.message = this._common_message.PhotoChangeMsg;
                        setInterval(() => {
                            this.showMessage = false;
                        }, 5000);
                    }
                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._common_message.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                }
            }).catch((res) => {
                this.showErrorMessage = true;
                console.log(res.error);
                this.message = this._common_message.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this._globalURL.isLoading = false;
            })
    }

    public chnageChequePhoto(metadata: any, refId: string, phtoId?: number) {
        var formData = new FormData();
        formData.append("RefId", refId);
        formData.append("file", metadata);
        this._fileUploadService.post((this._globalURL.ADD_PHOTO + "?folderName=BankReceipt" + "&type=" + this._globalURL.CMRC_TO_VO_FUND_CHECK_RECEIPT + "&RefId=" + refId + "&PhotoId=" + (phtoId == 0 || phtoId == null ? 0 : phtoId)),
            formData, sessionStorage.getItem("access_token")).then((res) => {
                if (res.Status == undefined) {
                    if (res.status == 200) {
                        this.getFund(this.globalVariables.APPROVED_FUND, 0)
                        this.showMessage = true;
                        this.message = this._common_message.PhotoChangeMsg;
                        setInterval(() => {
                            this.showMessage = false;
                        }, 5000);
                    }
                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._common_message.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                }
            }).catch((res) => {
                this.showErrorMessage = true;
                console.log(res.error);
                this.message = this._common_message.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this._globalURL.isLoading = false;
            })
    }
    reRenderDT() {
        debugger;
        this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
            dtInstance.destroy();
            this.dtTrigger.next();
        });
    }

}


