import { Component, ViewChild, AfterViewInit, OnInit } from "@angular/core";
import { GlobalAssets } from "../../../environments/Global";
import { CommonService } from "../../../services/common.service";
import { AccessToken } from "../../../environments/access_token";
import { Router } from "@angular/router";
import { LangulageConf } from "../../../environments/language.config";
import { DISTRICTService } from "../../../services/district.services";
import { DataTableDirective } from "angular-datatables";
import { Subject } from "rxjs";
import { OtherIncomeMasterModel } from "./otherIncome.model.component";
import { CommonMessageComponent } from "../../../environments/CommanMssage";
import { Title } from "../../../../../node_modules/@angular/platform-browser";
import { TitleConstants } from "../../../utils/constants/title-constants";

@Component({
    templateUrl: '../otherIncome/otherIncome.component.html',
    styleUrls: ['../../../components/app.css']
})


export class DistrictOtherIncomeComponent implements OnInit {
    public showMessage: boolean;
    public showErrorMessage: boolean;
    public showWarningMessage: boolean;
    public deleterecord: any;
    public ShowErrormsg: boolean;
    public Cmrcs: any;
    public Vos: any;
    public dtTrigger: Subject<any> = new Subject();
    @ViewChild(DataTableDirective)
    dtElement: DataTableDirective;
    dtInstance: DataTables.Api;
    public SHGNameAndId: any;
    public membersNameAndId: any;
    public selectedShgId: any;
    public message: any;
    public convertedWordsInEnglish: string;
    public convertedWordsInMarathi: string;
    public convertedWordsInHindi: string;
    public otherIncomeDetails: any;
    public totalDonationAmt: number;
    constructor(public _districtService: DISTRICTService,
        public _globalURL: GlobalAssets,
        public _commonService: CommonService,
        public _access_token: AccessToken,
        public _commonMessage: CommonMessageComponent,
        public _router: Router,
        public lang: LangulageConf,
        public otherIncomeData: OtherIncomeMasterModel,
        public titleService: Title) {
            this.setTitle(TitleConstants.DISTRICT_OTHER_INCOME_DETAILS);
    }

    ngOnInit() {
        this.getCMRCsKVP();
    }
    ngAfterViewInit(){
       // this.dtTrigger.next();
    }
    public setTitle(newTitle: string) {
        this.titleService.setTitle(newTitle);
    }
    getVoByCmrcId(selectCmrc: any): void {
        debugger;
        this._globalURL.isLoading = true;
        this._districtService.get((this._globalURL.GET_VO_BY_CMRC_ID + "?cmrcId=" + selectCmrc), sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.Vos = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this._globalURL.isLoading = false;
                    this.showWarningMessage = true;
                    this.message = this._commonMessage.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                }
            }).catch((error) => {
                this._globalURL.isLoading = false;
                this.showErrorMessage = true;
                this.message = this._commonMessage.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            })
    }

    getCMRCsKVP(): void {
        debugger;
        this._globalURL.isLoading = true;
        this._districtService.get(this._globalURL.GET_KVP_DISTRICT_CMRC_DASHBOARD, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.Cmrcs = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this._globalURL.isLoading = false;
                    this.showWarningMessage = true;
                    this.message = this._commonMessage.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    },5000);
                }
               // this.reRenderDT();
            }).catch((error) => {
                this._globalURL.isLoading = false;
                this.showErrorMessage = true;
                this.message = this._commonMessage.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            })
    }

    public selectedVoId: number = 0;
    getOtherIncomeDetails(voId: number) {
        debugger
        this.selectedVoId = voId;
        this.totalDonationAmt = 0;
        this._districtService.get(this._globalURL.GET_DISTRICT_OTHER_INCOME_DETAILS + voId, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status == 200) {
                    if (res.status == 200) {
                        this.otherIncomeDetails = JSON.parse(res._body).Data;
                        for (var item in this.otherIncomeDetails) {
                            this.totalDonationAmt = this.totalDonationAmt + this.otherIncomeDetails[item].Amount;
                            this._globalURL.isLoading = false;
                        }
                    }
                    else {
                        this.ShowErrormsg = true;
                        this.message = "Unable to get data";
                        setInterval(() => {
                            this._globalURL.isLoading = false;
                            this._globalURL.isSaveSuccessMessage = false;
                            this.ShowErrormsg = false;

                        }, 5000);
                    }
                }
                else {
                    if (res.Status == true) {
                        this.otherIncomeDetails = res.Data;
                        for (var item in this.otherIncomeDetails)
                            this.totalDonationAmt = this.totalDonationAmt + this.otherIncomeDetails[item].Amount;
                        this.ShowErrormsg = false;
                        this._globalURL.isLoading = false;
                    }
                    else {
                        this.message = "Unable to get Data";
                        setInterval(() => {
                            this._globalURL.isLoading = false;
                            this._globalURL.isSaveSuccessMessage = false;
                            this.ShowErrormsg = false;
                        }, 5000);
                    }
                }
            
            }).catch((error) => {
                this._globalURL.isLoading = false;
                this.showErrorMessage = true;
                this.message = this._commonMessage.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                },5000);
            })
    }

    public setUpdate(otherIncomeValues: any) {
        this.convertToWords(otherIncomeValues.Amount);
        this.otherIncomeData = otherIncomeValues;
    }

    public confirmUpdate(data: any) {
        debugger;
        let otherIncomeUrl = this._globalURL.UPDATE_DISTRICT_OTHER_INCOME_DETAILS + "?id=" + data.Id + "&amount=" + data.Amount + "&paidDate=" + data.PaidDate;
        this._districtService.post(otherIncomeUrl, null, sessionStorage.getItem("access_token"))
            .then((res) => {
                if ((res.Status) == undefined) {
                    if (res.status === 200) {
                        debugger;
                        this._globalURL.isLoading = false;
                        this.getOtherIncomeDetails(this.selectedVoId);
                        this.message = this._commonMessage.UpdateMsg;
                        this.showMessage = true;
                        setInterval(() => {
                            this.showMessage = false;
                        }, 5000);
                    }
                }
                else {
                    this._globalURL.isLoading = false;
                    this.showWarningMessage = true;
                    this.message = this._commonMessage.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                }

            }).catch((error) => {
                this._globalURL.isLoading = false;
                this.showErrorMessage = true;
                this.message = this._commonMessage.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            })
    }

    reRenderDT() {
        this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
            dtInstance.destroy();
            this.dtTrigger.next();
        });
    }

    fordelete(data: any) {
        this.deleterecord = data;
    }

    deleteOtherIncome(id: number) {
        debugger;
        this._districtService.get(this._globalURL.DELETE_OTHER_INCOME_DETAILS + id, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._commonMessage.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this._router.navigate(['../login']);
                }

                if (res.Status == true) {
                    this._globalURL.isLoading = false;
                    this.message = this._commonMessage.DeleteOtherIncomeMsg;
                    this.showMessage = true;
                    setInterval(() => {
                        this.showMessage = false;
                    }, 5000);
                    this.getOtherIncomeDetails(this.selectedVoId);
                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._commonMessage.WarningMsg;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this.getOtherIncomeDetails(this.selectedVoId);
                    this._globalURL.isLoading = false;
                }
            }).catch((res) => {
                this.showErrorMessage = true;
                this.message = this._commonMessage.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this.getOtherIncomeDetails(this.selectedVoId);
                this._globalURL.isLoading = false;
            })
    }

    convertToWords(num) {
        debugger;
        this.convertedWordsInEnglish = this._commonService.convertDigitToWordsInEnglish(num);
        this.convertedWordsInHindi = this._commonService.convertDigitToWordsInHindi(num);
        this.convertedWordsInMarathi = this._commonService.convertDigitToWordsInMarathi(num);
    }
}


