import { Component, OnInit, ViewChild, AfterViewInit } from '@angular/core';
import { VOService } from '../../../services/vo.services';
import { Router } from '@angular/router';
import { AccessToken } from '../../../environments/access_token';
import { VOMasterModel } from './vo.model.component';
import { GlobalAssets } from '../../../environments/Global';
import { CMRCService } from '../../../services/cmrc.services';
import { CommonService } from '../../../services/common.service';
import { DataTableDirective } from 'angular-datatables';
import { GlobalVariable } from '../../../environments/GlobalVeriables';
import { LangulageConf } from '../../../environments/language.config';
import { ExportToPDF } from '../../../services/reports/exportpdf';
import { ExportToExcel } from '../../../services/reports/exportexcel';
import { Subject } from 'rxjs';
import { NgForm } from '@angular/forms';
import { DatepickerOptions, NgDatepickerModule } from 'ng2-datepicker';
import * as frLocale from 'date-fns/locale/en';
import { FileUploadService } from '../../../services/FileService/file_upload.service';
import { CommonMessageComponent } from '../../../environments/CommanMssage';
import { Title } from '../../../../../node_modules/@angular/platform-browser';
import { TitleConstants } from '../../../utils/constants/title-constants';

@Component({
    templateUrl: '../../cmrc/vo/vo.component.html',
    styleUrls: ['../../../components/app.css']
})

export class VoComponent implements OnInit, AfterViewInit {
    public changePhotoDetails: any;
    public buttonDisabled: boolean;
    public validusername: boolean = false;
    public villagess: any;
    public showMessage: any;
    public showErrorMessage: any;
    public message: any;
    public disabled: boolean = true;
    @ViewChild(DataTableDirective)
    dtElement: DataTableDirective;
    dtInstance: DataTables.Api;
    public dtTrigger: Subject<any> = new Subject();
    public ShowErrormsg: boolean;
    public viewData: any;
    public getAllFundTypes: any;
    public vodetails: any;
    public nextCode: any;
    public BankAccountNumber: number;
    public BankAccountNumber_: number;
    public showWarningMessage: boolean;
    public ditricts: any;
    public FormationDate: Date = new Date();
    options: DatepickerOptions = {
        minYear: 2000,
        maxYear: (new Date().getFullYear() + 1),
        displayFormat: 'MMM D[,] YYYY',
        barTitleFormat: 'MMMM YYYY',
        dayNamesFormat: 'dd',
        firstCalendarDay: 0, // 0 - Sunday, 1 - Monday
        locale: frLocale,
        barTitleIfEmpty: 'Click to select a date'
    };
    constructor(public _cmrcService: CMRCService,
        public _commonService: CommonService,
        public voModel: VOMasterModel,
        public _globalURL: GlobalAssets,
        public _globalVariable: GlobalVariable,
        public _access_token: AccessToken,
        public exportToExcel: ExportToExcel,
        public exportToPDF: ExportToPDF,
        public _voService: VOService,
        public _router: Router,
        public lang: LangulageConf,
        public _common_message: CommonMessageComponent,
        public globalVariables: GlobalVariable,
        private _fileUploadService: FileUploadService,
        public titleService: Title) {
        if (sessionStorage.getItem("access_token") == "") {
            _router.navigate(['../login']);
        }
        this.setTitle(TitleConstants.CMRC_VO_DETAILS);
    }

    public setTitle(newTitle: string) {
        this.titleService.setTitle(newTitle);
    }
    ngOnInit(): void {
        this.viewData = {
        };
    }

    ngAfterViewInit(): void {
        this.dtTrigger.next();
        this.getVillageKVP();
        this.getVODetails();
        this.getFundType();
        this.getAllDistrict();
    }

    getNextCode(code: number) {
        this._commonService.get(this._globalURL.GET_NEXT_CODE + code + "&parentCode=" + this.globalVariables.cmrcBaseCode)
            .then((res) => {
                if (res.Status == true) {
                    this.nextCode = res.Data;
                    this.getFundType();
                }
            })
    }

    public VOForm: NgForm;
    checkUserName(userName: NgForm) {
        debugger;
        this._voService.get(this._globalURL.GET_CHECK_USERNAME + userName.value, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this._router.navigate(['../login']);
                }
                if (res.Status == true) {
                    this.validusername = true;
                    this.disabled = false;
                }
                else {
                    this.validusername = false;
                    this.disabled = true;
                }
            })
            .catch((err) => {
            })
    }

    getFundType() {
        this._cmrcService.get(this._globalURL.GET_FUND_TYPE_KET_VALUE_PAIR, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.getAllFundTypes = res.Data;
                }
            })
    }

    getAllDistrict(): void {
        this._globalURL.isLoading = true;
        this._cmrcService.get(this._globalURL.GET_DISTRICT, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this._router.navigate(['../login']);
                }
                if (res.Status == true) {
                    this.ditricts = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._common_message.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                }
            }).catch((err) => {
                this._globalURL.isLoading = false;
                this.message = this._common_message.CatchBlockMsg;
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            });
    }

    getVODetails() {
        this._cmrcService.get(this._globalURL.GET_VO_BY_CMRC, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.vodetails = res.Data;
                }
                this._globalURL.isLoading = false;
                this.reRenderDT();
            })
            .catch((err) => {
                this.ShowErrormsg = true;
                this.message = "Record is not created";
                setInterval(() => {
                    this.ShowErrormsg = false;
                    this._globalURL.isLoading = false;
                }, 5000);
            })
    }

    viewSHGDetail(data: any): void {
        this.viewData = data;
    }
    public voform: NgForm;
    createVO(vodata: NgForm, frDt: Date, metadata: any): void {
        debugger;
        let voname = vodata.value.VOName;
        this.voform = vodata.value;
        vodata.value.Code = this.nextCode;
        vodata.value.ParentCode = this.globalVariables.cmrcBaseCode;
        this._globalURL.isLoading = true;
        this._cmrcService.post(this._globalURL.POST_CREATE_VO, vodata.value, sessionStorage.getItem("access_token"))
            .then((res) => {
                debugger;
                if (res.Status == undefined) {
                    if (res.status === 200) {
                        var data = JSON.parse(res._body);
                        this._globalURL.isLoading = false;
                        this.showMessage = true;
                        this.message = "New VO - " + voname + " created Successfully";
                        setInterval(() => {
                            this.showMessage = false;
                        }, 5000);                        
                        var voAgreemeneFileFormData = new FormData();
                        voAgreemeneFileFormData.append("file", metadata);
                        voAgreemeneFileFormData.append("RefId", data.Data);
                        this._fileUploadService.post((this._globalURL.ADD_PHOTO + "?folderName=VOAgreementPhotos" + "&type=" + this._globalURL.VO_AGREEMENT_PHOTO + "&RefId=" + data.Data + "&PhotoId=0"), voAgreemeneFileFormData, sessionStorage.getItem("access_token"))
                            .then((res) => {
                                this.getVODetails();
                                if (res.Status == undefined) {
                                    if (res.status == 200) {
                                        this._globalURL.isLoading = false;
                                        vodata.control.reset();
                                        this.showMessage = true;
                                        this.message = this._common_message.PhotoUploadMsg;
                                        setInterval(() => {
                                            this.showMessage = false;
                                        }, 5000);
                                    }
                                    else {
                                        this._globalURL.isLoading = false;
                                        this.message = this._common_message.CatchBlockMsg;
                                        this.showErrorMessage = true;
                                        setInterval(() => {
                                            this.showErrorMessage = false;
                                        }, 5000);
                                    }
                                }
                            }).catch((res) => {
                                this._globalURL.isLoading = false;
                                this.message = this._common_message.CatchBlockMsg;
                                this.showErrorMessage = true;
                                setInterval(() => {
                                    this.showErrorMessage = false;
                                }, 5000);
                            })
                    }
                    else {
                        this.ShowErrormsg = true;
                        this.message = "Record is not create";
                        setInterval(() => {
                            this._globalURL.isSaveSuccessMessage = false;
                            this.ShowErrormsg = false;
                        }, 5000);
                    }
                }
                else {
                    if (res.Data === "Success") {
                        var data = JSON.parse(res._body);
                        this._globalURL.isLoading = false;
                        this.getVODetails();
                        this.showMessage = true;
                        this.message = "New VO - " + data.VOName + "Addedd Successfully";
                        setInterval(() => {
                            this.showMessage = false;
                        }, 5000);
                    }
                }
            })
            .catch((err) => {
                this.ShowErrormsg = true;
                this.message = "Record is not created";
                setInterval(() => {
                }, 5000);
            })

    }
    getVillageKVP(): void {
        debugger;
        this._globalURL.isLoading = true;
        this._cmrcService.get(this._globalURL.KVPGET_VILLAGE, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.villagess = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this._globalURL.isLoading = false;
                    this.message = this._common_message.WarningMsg;
                    this.showErrorMessage = true;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                }
            }).catch((err) => {
                this._globalURL.isLoading = false;
                this.message = this._common_message.CatchBlockMsg;
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            });
    }

    //Download pdf Anjali 
    R36_VoExportTopdf() {
        this._globalURL.isLoading = true;
        var topData = {
            "cmrcBaseCode": this._globalVariable.cmrcBaseCode,
        };
        this.exportToPDF.R36_VoExportTopdf(this.vodetails, topData);
        this._globalURL.isLoading = false;
    }
    public R36_VoViewExportTopdf(data: any) {
        this._globalURL.isLoading = true;
        this.exportToPDF.R36_VoViewExportTopdf(data)
        this._globalURL.isLoading = false;
    }

    public listOfVoExcel() {
        this.exportToExcel.listOfVoExcel(this.vodetails);
    }

    reRenderDT() {
        this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
            dtInstance.destroy();
            this.dtTrigger.next();
        });
    }

    forchange(data:any){
        this.changePhotoDetails = data;
    }

    public chnagePhoto(metadata: any, refId: string, phtoId?: number) {
        debugger;
        var formData = new FormData();
        formData.append("RefId", refId);
        formData.append("file", metadata);
        this._fileUploadService.post((this._globalURL.ADD_PHOTO + "?folderName=VOAgreementPhotos" + "&type=" + this._globalURL.VO_AGREEMENT_PHOTO + "&RefId=" + refId + "&PhotoId=" + (phtoId == 0 || phtoId == null ? 0 : phtoId)),
            formData, sessionStorage.getItem("access_token")).then((res) => {
                if (res.Status == undefined) {
                    if (res.status == 200) {
                        this.getVODetails();
                        this.showMessage = true;
                        this.message = this._common_message.PhotoChangeMsg;
                        setInterval(() => {
                            this.showMessage = false;
                        }, 5000);
                    }
                }
                else {
                    this.showErrorMessage = true;
                    this.message = this._common_message.WarningMsg;
                    this.message = this._common_message.WarningMsg;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                }
            }).catch((res) => {
                this.showErrorMessage = true;
                console.log(res.error);
                this.message = this._common_message.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this._globalURL.isLoading = false;
            })
    
}
}


