//Anjali...
import { Component, OnInit, ViewChild } from '@angular/core';
import { CurrencyPipe } from '@angular/common';
import { constructor } from 'events';
import { Router } from '@angular/router';
import { AccessToken } from '../../../environments/access_token';
import { GlobalAssets } from '../../../environments/Global';
import { CMRCService } from '../../../services/cmrc.services';
import { GlobalVariable } from '../../../environments/GlobalVeriables';
import { CommonService } from '../../../services/common.service';
import { LangulageConf } from '../../../environments/language.config';
import { CommonMessageComponent } from '../../../environments/CommanMssage';
import { NgForm } from '@angular/forms';
import { DatepickerOptions } from 'ng2-datepicker';
import * as frLocale from 'date-fns/locale/en';
import { FileUploadService } from '../../../services/FileService/file_upload.service';
import { Title } from '../../../../../node_modules/@angular/platform-browser';
import { TitleConstants } from '../../../utils/constants/title-constants';
import { DataTableDirective } from '../../../../../node_modules/angular-datatables';
import { Subject } from '../../../../../node_modules/rxjs';


@Component({
    templateUrl: '../fd/fd.component.html',
    styleUrls: ['../../../components/app.css']
})
export class FDComponent implements OnInit {
    @ViewChild(DataTableDirective)
    dtElement: DataTableDirective;
    dtInstance: DataTables.Api;
    public dtTrigger: Subject<any> = new Subject();
    public changePhotoDetails: any;
    public showWarningMessage: boolean;
    public showErrorMessage: boolean;
    public FDId: number;
    public shgs: any;
    public ShowErrormsg: boolean;
    public Vos: any;
    public convertedWordsInEnglish: string;
    public convertedWordsInMarathi: string;
    public convertedWordsInHindi: string;
    public fdModel: any;
    public getAllFDs: any;
    public totalAmt: number = 0;
    public message: string;
    public showMessage: boolean;
    public vos: any;
    public members: any;
    public totalReturnableAmt: number = 0;
    public CreatedDate: Date = new Date();
    options: DatepickerOptions = {
        minYear: 2000,
        maxYear: (new Date().getFullYear() + 1),
        displayFormat: 'MMM D[,] YYYY',
        barTitleFormat: 'MMMM YYYY',
        dayNamesFormat: 'dd',
        firstCalendarDay: 0, // 0 - Sunday, 1 - Monday
        locale: frLocale,
        barTitleIfEmpty: 'Click to select a date'
    };
    public showmember: boolean = false;
    public status1: number = 1;
    public status2:number = 2;
    constructor(public _cmrcservice: CMRCService,
        public _router: Router,
        public lang: LangulageConf,
        public _access_token: AccessToken,
        public _globalURL: GlobalAssets,
        public _globalVariable: GlobalVariable,
        public _commonService: CommonService,
        public _common_message: CommonMessageComponent,
        private _fileUploadService: FileUploadService,
        public titleService: Title) {
        this.fdModel = null;
        if (sessionStorage.getItem("access_token") == "") {
            _router.navigate(['../login']);
        }
        this.setTitle(TitleConstants.CMRC_FD_DETAILS)
        this.getAllFDs = null;
    }

    public setTitle(newTitle: string) {
        this.titleService.setTitle(newTitle);
    }
    ngOnInit() {
        this.getFD(1);
        this.getAllVo();
    }

    ngAfterViewInit() : void{
        this.dtTrigger.next();
    }

    convertToWords(num) {
        this.convertedWordsInEnglish = this._commonService.convertDigitToWordsInEnglish(num);
        this.convertedWordsInHindi = this._commonService.convertDigitToWordsInHindi(num);
        this.convertedWordsInMarathi = this._commonService.convertDigitToWordsInMarathi(num);
    }

    getAllVo() {
        debugger;
        this._globalURL.isLoading = true;
        this._cmrcservice.get(this._globalURL.GET_VO_BY_CMRC_ID, sessionStorage.getItem("access_token"))
            .then((res) => {
                    if (res.Status == true) {
                        this.vos = res.Data;
                        this._globalURL.isLoading = false;
                    }
                    else {
                        this._globalURL.isLoading = false;
                    }
            }).catch((res) => {
                this.message = this._common_message.CatchBlockMsg;
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                    this._globalURL.isLoading = false;
                }, 5000);
            })
    }

    getSHGByVO(voId: number) {
        this._globalURL.isSaveSuccessMessage = true;
        this._globalURL.isLoading = true;
        this._cmrcservice.get(this._globalURL.GET_SHG_NAME_AND_ID_VOID + voId, sessionStorage.getItem("access_token"))
            .then((res) => {
                    if (res.Status == true) {
                        this.shgs = res.Data;
                        this._globalURL.isLoading = false;
                    }
                    else {
                        this._globalURL.isLoading = false;
                    }
                this.reRenderDT();
            }).catch((res) => {
                this._globalURL.isLoading = false;
                this.message = this._common_message.CatchBlockMsg;
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            })
    }

    getMemberOrSHG(id:number){
        id == 2 ? this.showmember = true : this.showmember = false; 
    }
    getMembersBySHG(shgId:any){
        this._globalURL.isSaveSuccessMessage = true;
        this._cmrcservice.get(this._globalURL.GET_MEM_ID_NAME + shgId, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.members = res.Data;
                }
                else {
                    this._globalURL.isLoading = false;
                }
                this.reRenderDT();
            }).catch((res) => {
                this._globalURL.isLoading = false;
                this.message = this._common_message.CatchBlockMsg;
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            })
    }
    //GET ALL Fund Type 
    getFD(status: number): void {
        debugger;
        this._globalURL.isLoading = true;
        this.FDId = null;
        this.FDId = status;
        this.totalAmt = 0;
        this.totalReturnableAmt = 0;
        this.getAllFDs = null;
        this._cmrcservice.get((this._globalURL.GET_FD + this.FDId), sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this._router.navigate(['../login']);
                }
                if (res.Status == true) {
                    this.getAllFDs = res.Data;
                    this._globalURL.isLoading = false;
                    for (var item in this.getAllFDs) {
                        this.totalAmt = this.totalAmt + this.getAllFDs[item].Amount;
                        this.totalReturnableAmt = this.totalReturnableAmt + this.getAllFDs[item].TotalReturnAmount;
                    }
                }
                else {
                    this._globalURL.isLoading = false;;
                }
            }).catch((res) => {
                this._globalURL.isLoading = false;
                this.message = this._common_message.CatchBlockMsg;
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            })
    }

    createFD(data: NgForm, metadata: any) {
        debugger;
        this.showmember = false;
        this._globalURL.isLoading = true;
        this._cmrcservice.post((this._globalURL.CREATE_FD), data.value, sessionStorage.getItem("access_token"))
            .then((res) => {
                debugger;
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this._router.navigate(['../login']);
                }
                if (res.Status == undefined)
                    if (res.status == 200) {
                        this.getFD(1);
                        let data_ = JSON.parse(res._body);
                        if (data_.Status == true) {
                            data.control.reset();
                            this._globalURL.isLoading = false;
                            this.showMessage = true;
                            this.message = this._common_message.FDAddMsg;
                            setInterval(() => {
                                this.showMessage = false;
                            }, 5000);
                            var memberFDFile = new FormData();
                            memberFDFile.append("file", metadata);
                            memberFDFile.append("RefId", data_.Data);
                            this._fileUploadService.post((this._globalURL.ADD_PHOTO + "?folderName=MemberFD" + "&type=" + this._globalURL.FD_APPLICATION_PHOTO + "&RefId=" + data_.Data + "&PhotoId=0"), memberFDFile, sessionStorage.getItem("access_token"))
                                .then((res) => {
                                    if (res.Status == undefined) {
                                        if (res.status == 200) {
                                            this._globalURL.isLoading = false;
                                            this.showMessage = true;
                                            this.message = this._common_message.PhotoUploadMsg;
                                            setInterval(() => {
                                                this.showMessage = false;
                                            }, 5000);
                                            data.control.reset();
                                        }
                                        else {
                                            this.showWarningMessage = true;
                                            this.message = this._common_message.WarningMsg;
                                            setInterval(() => {
                                                this.showWarningMessage = false;
                                            }, 5000);
                                            this._globalURL.isLoading = false;
                                        }
                                    }
                                }).catch((res) => {
                                    this._globalURL.isLoading = false;
                                    this.message = this._common_message.CatchBlockMsg;
                                    this.showErrorMessage = true;
                                    setInterval(() => {
                                        this.showErrorMessage = false;
                                    }, 5000);
                                })
                        }
                        else {
                            this.showErrorMessage = true;
                            this.message = this._common_message.NotFDAddMsg;
                            setInterval(() => {
                                this.showErrorMessage = false;
                            }, 5000);
                            this._globalURL.isLoading = false;
                        }
                    }
                    else {
                        this.showErrorMessage = true;
                        this.message = this._common_message.NotFDAddMsg;
                        setInterval(() => {
                            this.showErrorMessage = false;
                        }, 5000);
                        this._globalURL.isLoading = false;
                    }
                else {
                    if (res.Status == 1) {
                        data.control.reset();
                        this._globalURL.isLoading = false;
                        this.showMessage = true;
                        this.message = this._common_message.FDAddMsg;
                        setInterval(() => {
                            this.showMessage = false;
                        }, 5000);
                        this.getFD(1);
                    }
                }
            })
            .catch((err) => {
                this._globalURL.isLoading = false;
                this.message = this._common_message.CatchBlockMsg;
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            })
    }

    delete(fordeletedata: any) {
        this.fdModel = fordeletedata;
    }

    public RetFDPaymentMode: number = 0;
    deleteFD(deletedata: any): void {
        deletedata.Status = 2;
        this._globalURL.isLoading = true;
        deletedata.PaymentMode = this.RetFDPaymentMode;
        this._cmrcservice.post(this._globalURL.UPDATE_FD, deletedata, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this._router.navigate(['../login']);
                }
                if (res.Status == undefined) {
                    if (res.status == 200) {
                        this.message = this._common_message.ReturnFDMsg;
                        this.showMessage = true;
                        setInterval(() => {
                            this.showMessage = false;
                        }, 5000);
                        this._globalURL.isLoading = false;
                        this.getFD(1);
                    }
                    else {
                        this.showErrorMessage = true;
                        this.message = this._common_message.FDNotReturnMsg;
                        setInterval(() => {
                            this.showErrorMessage = false;
                        }, 5000);
                        this._globalURL.isLoading = false;
                        this.getFD(1);
                    }
                }
                else {
                    if (res.Status == true) {
                        this.message = this._common_message.ReturnFDMsg;
                        this.showMessage = true;
                        setInterval(() => {
                            this.showMessage = false;
                        }, 5000);
                        this._globalURL.isLoading = false;
                        this.getFD(1);
                    }
                    else {
                        this.showErrorMessage = true;
                        this.message = this._common_message.FDNotReturnMsg;
                        setInterval(() => {
                            this.showErrorMessage = false;
                        }, 5000);
                        this._globalURL.isLoading = false;
                        this.getFD(1);
                    }
                }
            }).catch((err) => {
                this._globalURL.isLoading = false;
                this.message = this._common_message.CatchBlockMsg;
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this.getFD(1);
            })
    }

    forchange(data: any) {
        this.changePhotoDetails = data;
    }

    public chnagePhoto(metadata: any, refId: string, phtoId?: number) {
        debugger;
        var formData = new FormData();
        formData.append("RefId", refId);
        formData.append("file", metadata);
        this._fileUploadService.post((this._globalURL.ADD_PHOTO + "?folderName=MemberFD" + "&type=" + this._globalURL.FD_APPLICATION_PHOTO + "&RefId=" + refId + "&PhotoId=" + (phtoId == 0 || phtoId == null ? 0 : phtoId)),
            formData, sessionStorage.getItem("access_token")).then((res) => {
                if (res.Status == undefined) {
                    if (res.status == 200) {
                        this.getFD(1);
                        this.showMessage = true;
                        this.message = this._common_message.PhotoChangeMsg;
                        setInterval(() => {
                            this.showMessage = false;
                        }, 5000);
                    }
                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._common_message.WarningMsg;
                    this.message = this._common_message.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                }
            }).catch((res) => {
                this.showErrorMessage = true;
                console.log(res.error);
                this.message = this._common_message.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this._globalURL.isLoading = false;
            })

    }

    reRenderDT() {
        this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
            dtInstance.destroy();
            this.dtTrigger.next();
        });
    }
}



