import { DashboardDataChoiceSelector } from "./dashboard-selector";
import { GlobalVariable } from "../environments/GlobalVeriables";
import { Injectable } from "@angular/core";

@Injectable()
export class DashboardURLs {

    public static setUrl(baseUrl: string, voId?: number, roleId?: number): string {

        if (voId != 0 && voId != undefined && roleId != undefined) {
            return baseUrl + "?Id=" + voId + "&roleId=" + roleId;
        }
        return baseUrl;
    }

    public static setOneParameterURL(baseUrl: string, id?: number) {
        if (id != undefined && id != 0)
            return baseUrl + "?cmrcId=" + id;
        return baseUrl;
    }

    public static setOneParameterURLCMRCAppend(baseUrl: string, id?: number) {
        if (id != undefined && id != 0)
            return baseUrl + "&cmrcId=" + id;
        return baseUrl;
    }

    public static setOneParameterURLVO(baseUrl: string, id?: number) {
        if (id != undefined && id != 0)
            return baseUrl + "?voId=" + id;
        return baseUrl;
    }

    public static setURLWithRoleAndID(baseUrl: string, id?: number, roleId?: number) {
        if (id != 0 && id != undefined && roleId != 0 && roleId != undefined) {
            return baseUrl + "?Id=" + id + "&roleId=" + roleId;
        }
        return baseUrl;
    }

    public static setCMRCUrl(baseUrl: string): string {
        return baseUrl;
    }

    public static setBalancesheetUrl(baseUrl: string, id?: number, roleId?: number): string {
        if (id != 0 && id != undefined && roleId != 0 && roleId != undefined) {
            return baseUrl + "&Id=" + id + "&roleId=" + roleId;
        }
        return baseUrl;
    }

    public static setDistrictUrl(baseUrl: string): string {
        return baseUrl;
    }
}


