import { Injectable } from "@angular/core";

@Injectable()
export class VoDetailsModel {
    public BankAddress: string;
    public BranchName: string;
    public BankName: string;
    public IFSCCode: string;
    public BankAccountNumber: string;
    public VOName: any;
    public Id: number;  
    public Address: any;
    public District: string;
    public FormationDate: any;
    public Taluka: any;
    public AreaId:any;
    public PanCardNumber: any;
    constructor() {
        this.VOName = '';
        this.Id = 0;  
        this.FormationDate = '';
        this.Address = '';
        this.District = '';
        this.Taluka = '';
        this.AreaId = '';
        this.PanCardNumber = '';
        this.BankAccountNumber = '';
        this.IFSCCode = '';
        this.BankName = '';
        this.BranchName = '';
        this.BankAddress = '';
    }

} 





