import { Component, OnInit, ViewChild, AfterViewInit } from '@angular/core';
import { constructor } from 'events';
import { Router } from '@angular/router';
import { AccessToken } from '../../../environments/access_token';
import { GlobalAssets } from '../../../environments/Global';
import { CMRCService } from '../../../services/cmrc.services';
import { GlobalVariable } from '../../../environments/GlobalVeriables';
import { LangulageConf } from '../../../environments/language.config';
import { DataTableDirective } from 'angular-datatables';
import { Subject } from 'rxjs';
import { CommonMessageComponent } from '../../../environments/CommanMssage';
import { NgForm } from '@angular/forms';
import { Title } from '../../../../../node_modules/@angular/platform-browser';
import { TitleConstants } from '../../../utils/constants/title-constants';


@Component({
    templateUrl: '../occupation/occupation.component.html',
    styleUrls: ['../../../components/app.css']
})
export class OccupationComponent implements OnInit, AfterViewInit {
    showWarningMessage: boolean;
    showErrorMessage: boolean;
    @ViewChild(DataTableDirective)
    dtElement: DataTableDirective;
    dtInstance: DataTables.Api;
    public dtTrigger: Subject<any> = new Subject();
    public ShowErrormsg: boolean;
    public getOccupations: any;
    public occupationModel: any;
    public talukaModel: any;
    public getMasterTalukas: any;
    public message: string;
    public showMessage: boolean;

    constructor(public _cmrcservice: CMRCService,
        public _router: Router,
        public lang: LangulageConf,
        public _access_token: AccessToken,
        public _globalURL: GlobalAssets,
        public _globalVariable: GlobalVariable,
        public _common_message: CommonMessageComponent,
        public titleService: Title
    ) {
        this.setTitle(TitleConstants.CMRC_OCCUPATION_DETAILS);
        _globalURL.isLoading = true;
        if (sessionStorage.getItem("access_token") == "") {
            _router.navigate(['../login']);
        }
    }
    ngOnInit() {
        this.getOccupation();
    }
    ngAfterViewInit(): void {
        this.dtTrigger.next();
        this.getOccupation();
    }
    public setTitle(newTitle: string) {
        this.titleService.setTitle(newTitle);
    }
    //GET AREA AND ...
    getOccupation(): void {
        this._globalURL.isLoading = true;
        debugger;
        this._cmrcservice.get(this._globalURL.GET_OCCUPATION_DETAILS, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.getOccupations = res.Data;
                    this._globalURL.isLoading = false;

                }
                else {
                    this._globalURL.isLoading = false;
                    this.showWarningMessage = true;
                    this.message = this._common_message.WarningMsg
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                }
                this.reRenderDT();
            }).catch((res) => {
                this._globalURL.isLoading = false;
                this.message = this._common_message.CatchBlockMsg;
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            })
    }

    createOccupation(Data: NgForm): void {
        this._globalURL.isLoading = true;
        debugger;
        let occupation = Data.value.Occupation;
        this._cmrcservice.post((this._globalURL.CREATE_OCCUPATION + occupation), Data.value, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == undefined)
                    if (res.status == 200) {
                        let data_ = JSON.parse(res._body);
                        if (data_.Status == true) {
                            Data.control.reset();
                            this._globalURL.isLoading = false;
                            this.message = this._common_message.AddOccupationMsg;
                            this.showMessage = true;
                            setInterval((res) => {
                                this.showMessage = false;
                            }, 5000)
                            this.getOccupation();
                        }
                        else {
                            this._globalURL.isLoading = false;
                            this.message = this._common_message.ErrorAddOccupationMsg;
                            this.showErrorMessage = true;
                            setInterval((res) => {
                                this.showErrorMessage = false;
                            }, 5000)
                        }
                    }
                    else {
                        this._globalURL.isLoading = false;
                        this.message = this._common_message.ErrorAddOccupationMsg;
                        this.showErrorMessage = true;
                        setInterval((res) => {
                            this.showErrorMessage = false;
                        }, 5000)
                    }
                else {
                    if (res.Status == 1) {
                        this._globalURL.isLoading = false;
                        this.message = this._common_message.AddOccupationMsg;
                        this.showMessage = true;
                        setInterval((res) => {
                            this.showMessage = false;
                        }, 5000)
                        this.getOccupation();
                    }
                    else {
                        this._globalURL.isLoading = false;
                        this.message = this._common_message.ErrorAddOccupationMsg;
                        this.showErrorMessage = true;
                        setInterval((res) => {
                            this.showErrorMessage = false;
                        }, 5000)
                    }
                }
            }).catch((err) => {
                this._globalURL.isLoading = false;
                this.message = this._common_message.CatchBlockMsg;
                this.showErrorMessage = true;
                setInterval((res) => {
                    this.showErrorMessage = false;
                }, 5000)
            })
    }

    delete(data: any) {
        debugger;
        this.occupationModel = data;
        debugger;
    }

    deleteOccupation(data: any): void {
        this._globalURL.isLoading = true;
        debugger;
        let id = data.Id;
        let final = this._globalURL.DELETE_OCCUPATION + id;
        this._cmrcservice.post(final, "data", sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == undefined) {
                    var data = JSON.parse(res._body);
                    if (data.Status == true) {
                        this._globalURL.isLoading = false;
                        this.message = this._common_message.OccupationDeleteMsg;
                        this.showMessage = true;
                        setInterval((res) => {
                            this.showMessage = false;
                        }, 5000)
                        this.getOccupation();
                    }
                    else {
                        this._globalURL.isLoading = false;
                        this.message = this._common_message.ErrorOccupationDeleteMsg;
                        this.showErrorMessage = true;
                        setInterval((res) => {
                            this.showErrorMessage = false;
                        }, 5000)
                    }
                }
                else {
                    if (res.Status == true) {
                        this._globalURL.isLoading = false;
                        this.message = this._common_message.OccupationDeleteMsg;
                        this.showMessage = true;
                        setInterval((res) => {
                            this.showMessage = false;
                        }, 5000)
                        this.getOccupation();
                    }
                    else {
                        this._globalURL.isLoading = false;
                        this.message = this._common_message.ErrorOccupationDeleteMsg;
                        this.showErrorMessage = true;
                        setInterval((res) => {
                            this.showErrorMessage = false;
                        }, 5000)
                    }
                }
            }).catch((err) => {
                this._globalURL.isLoading = false;
                this.message = this._common_message.CatchBlockMsg;
                this.showErrorMessage = true;
                setInterval((res) => {
                    this.showErrorMessage = false;
                }, 5000)
            })
    }
    reRenderDT() {
        this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
            dtInstance.destroy();
            this.dtTrigger.next();
        });
    }
}













