//Anjali..
import { Component, OnInit, ViewChild, AfterViewInit } from '@angular/core';
import { CurrencyPipe } from '@angular/common';
import { constructor } from 'events';
import { Router } from '@angular/router';
import { AccessToken } from '../../../environments/access_token';
import { GlobalAssets } from '../../../environments/Global';
import { CMRCService } from '../../../services/cmrc.services';
import { GlobalVariable } from '../../../environments/GlobalVeriables';
import { LangulageConf } from '../../../environments/language.config';
import { Title } from '../../../../../node_modules/@angular/platform-browser';
import { TitleConstants } from '../../../utils/constants/title-constants';
import { DataTableDirective } from '../../../../../node_modules/angular-datatables';
import { Subject } from '../../../../../node_modules/rxjs';
import { VOService } from '../../../services/vo.services';
import { CommonMessageComponent } from '../../../environments/CommanMssage';
import { CommonService } from '../../../services/common.service';
import { MemberShareModel } from './memberShare.model';
import { controlNameBinding } from '@angular/forms/src/directives/reactive_directives/form_control_name';

@Component({
    templateUrl: '../member_share/membershare.component.html',
    styleUrls: ['../../../components/app.css']
})

export class MemberShareComponent implements OnInit, AfterViewInit {
    @ViewChild(DataTableDirective)
    dtElement: DataTableDirective;
    dtInstance: DataTables.Api;
    public dtTrigger: Subject<any> = new Subject();
    public MemberId: string;
    public shareAmount: string;
    public memberId: string;
    public memberShareModel: any;
    public showMessage: boolean;
    public message: string;
    public ShowErrormsg: boolean;
    public selectedShgId: number;
    public selectedVoId: number;
    public shgs: any;
    public Vos: any;
    public memShares: MemberShareModel;
    public showSHGdropdown: boolean = false;
    public showMemDrop: boolean = false;
    public members: any;
    public showErrorMessage: boolean;
    public convertedWordsInEnglish: string;
    public convertedWordsInMarathi: string;
    public convertedWordsInHindi: string;
    public totalAmt: number = 0;
    public shareModel: MemberShareModel;
    public giveAllAmt: boolean = false;
    public giveIndividualAmt: boolean = false;
    public dataforIndividualamt: any;
    // public voId: any;
    constructor(public _voservice: VOService,
        public _router: Router,
        public lang: LangulageConf,
        public _access_token: AccessToken,
        public _globalURL: GlobalAssets,
        public _globalVariable: GlobalVariable,
        public _cmrcService: CMRCService,
        public _commonService: CommonService,
        public _common_message: CommonMessageComponent,
        public titleService: Title
    ) {
        // this.selectedVoId = ;
        _globalURL.isLoading = true;
        if (sessionStorage.getItem("access_token") == "") {
            _router.navigate(['../login']);
        }
        this.memberShareModel = null;
        this.setTitle(TitleConstants.CMRC_MEMBER_FEES);
    }
    public setTitle(newTitle: string) {
        this.titleService.setTitle(newTitle);
    }

    ngOnInit() {
        this.getShgNameAndId();
    }

    ngAfterViewInit(): void {
        this.dtTrigger.next();
        this.getMemShareDetails();
    }

    getMemberOrSHG(id: any) {
        if (id == 1) {
            this.showSHGdropdown = true;
        } else {
            this.showSHGdropdown = true, this.showMemDrop = true;
        }
    }

    //Get member details ...
    public shgId = this.selectedShgId;
    public voId = this.selectedVoId;
    getMemShareDetails(): void {
        this._globalURL.isLoading = true;
        debugger;
        this.memShares = null;
        this._voservice.get((this._globalURL.GET_MEMBER_SHARE_AMOUNT),
            sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.memShares = res.Data;
                    for (var item in this.memShares) {
                        this.totalAmt = this.totalAmt + this.memShares[item].Amount;
                        this.memShares[item].MemberName == null ? this.memShares[item].ContributedBy = "SHG" : this.memShares[item].ContributedBy = "MEMBER";
                        this.memShares[item].MemberName == null ? this.memShares[item].MemberName = "-" : this.memShares[item].MemberName;
                    }
                    this._globalURL.isLoading = false;
                }
                else {
                    this._globalURL.isLoading = false;
                }
                this.reRenderDT();
            }).catch((res) => {
                this._globalURL.isLoading = false;
            });
    }



    //get shg dropdown..
    getShgNameAndId(): void {
        debugger;
        this._globalURL.isLoading = true;
        debugger;
        this._voservice.get((this._globalURL.GET_SHG_NAME_ID_CMRC), sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.shgs = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this._globalURL.isLoading = false;
                }
            });
    }
    getMembersBySHG(shgId: number) {
        this._globalURL.isSaveSuccessMessage = true;
        this._voservice.get(this._globalURL.GET_MEM_ID_NAME + shgId, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.members = res.Data;
                }
                else {
                    this._globalURL.isLoading = false;
                }
                this.reRenderDT();
            }).catch((res) => {
                this._globalURL.isLoading = false;
                this.message = this._common_message.CatchBlockMsg;
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            })
    }

    delete(data: any) {
        this.memberShareModel = data;
    }
    saveSLEntry(data: any) {
        debugger;
        this.showSHGdropdown  = false;
        this.showMemDrop = false;
        this._voservice.post(this._globalURL.ADD_SHARE_AMOUNT, data.value, sessionStorage.getItem("access_token"))
            .then((res) => {  
                if (res.status == 200) {
                    data.control.reset();
                    this._globalURL.isLoading = false;
                    this.showMessage = true;
                    this.message = this._common_message.sladdmsg;
                    setInterval(() => {
                        this.showMessage = false;
                    }, 5000);
                    this.getMemShareDetails();
                }
                else {
                    this.ShowErrormsg = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this._globalURL.isSaveSuccessMessage = false;
                        this.ShowErrormsg = false;
                    }, 5000);
                    this.getMemShareDetails();
                }
            })
            .catch((err) => {
                // alert("sooory");
                setInterval(() => {
                }, 5000);
                this.getMemShareDetails();
            })
    }

    updateShareAmount(data: any) {
        debugger;
        this.getMembersBySHG(data.SHGId);
        this.shareModel = data;
    }

    updateSLEntry(data: any) {
        debugger;
        this._voservice.post(this._globalURL.UPDATE_SHARE_AMOUNT_VO, data, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status == 200) {
                    this._globalURL.isLoading = false;
                    this.showMessage = true;
                    this.message = this._common_message.sladdmsg;
                    setInterval(() => {
                        this.showMessage = false;
                    }, 5000);
                    this.getMemShareDetails();
                }
                else {
                    this.ShowErrormsg = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this._globalURL.isSaveSuccessMessage = false;
                        this.ShowErrormsg = false;
                    }, 5000);
                    this.getMemShareDetails();
                }
            })
            .catch((err) => {
                // alert("sooory");
                setInterval(() => {
                }, 5000);
                this.getMemShareDetails();
            })
    }

    returnMemberAllShareAmt(){
        this.giveAllAmt = false;
        this._voservice.post(this._globalURL.GIVE_ALL_SHARE_AMOUNT_CMRC , null, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status == 200) {
                    this._globalURL.isLoading = false;
                    this.showMessage = true;
                    this.message =this._common_message.Sharetocmrcmsg;
                    setInterval(() => {
                        this.showMessage = false;
                    }, 5000);
                    this.getMemShareDetails();
                }
                else {
                    this.ShowErrormsg = true;
                    this.message = "Record is not delete";
                    setInterval(() => {
                        this._globalURL.isSaveSuccessMessage = false;
                        this.ShowErrormsg = false;
                    }, 5000);
                    this.getMemShareDetails();
                }
            })
            .catch((err) => {
                // alert("sooory");
                setInterval(() => {
                }, 5000);
                this.getMemShareDetails();
            })
    }

    returnMemberShare(): void {
        debugger;
        this.giveIndividualAmt = false;
        this.dataforIndividualamt.RefId == undefined ? this.dataforIndividualamt.RefId = 0 : this.dataforIndividualamt.RefId;
        this._voservice.post(this._globalURL.GIVE_SHARE_AMOUNT_CMRC + this.dataforIndividualamt.RefId, null, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status == 200) {
                    this._globalURL.isLoading = false;
                    this.showMessage = true;
                    this.message = this._common_message.Sharetocmrcmsg;
                    setInterval(() => {
                        this.showMessage = false;
                    }, 5000);
                    this.getMemShareDetails();
                }
                else {
                    this.ShowErrormsg = true;
                    this.message = "Record is not delete";
                    setInterval(() => {
                        this._globalURL.isSaveSuccessMessage = false;
                        this.ShowErrormsg = false;
                    }, 5000);
                    this.getMemShareDetails();
                }
            })
            .catch((err) => {
                // alert("sooory");
                setInterval(() => {
                }, 5000);
                this.getMemShareDetails();
            })
    }
    giveAllAmount() {
        this.giveAllAmt = true;
        this.dataforIndividualamt = this.memShares;
    }
    forGiveToCMRC(data: any) {
        this.giveIndividualAmt = true;
        this.dataforIndividualamt = data;
    }
    reRenderDT() {
        this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
            dtInstance.destroy();
            this.dtTrigger.next();
        });
    }
    convertToWordsReq(num) {
        this.convertedWordsInEnglish = this._commonService.convertDigitToWordsInEnglish(num);
        this.convertedWordsInHindi = this._commonService.convertDigitToWordsInHindi(num);
        this.convertedWordsInMarathi = this._commonService.convertDigitToWordsInMarathi(num);
    }
}


