import { Component, OnInit } from '@angular/core';
import { constructor } from 'events';
import { Router } from '@angular/router';
import { CMRCService } from '../../../../services/cmrc.services';
import { AccessToken } from '../../../../environments/access_token';
import { GlobalAssets } from '../../../../environments/Global';
import { ExportToExcel } from '../../../../services/reports/exportexcel';
import { ExportToPDF } from '../../../../services/reports/exportpdf';
@Component({
    templateUrl: 'shg_Active_withdrawal.component.html',
    providers: [CMRCService]
})

export class ActiveAndWithdrawlShgComponent implements OnInit {
    ShgsByCmrc: any;
   public allActiveCmrcShgs: any;
   // public allActiveCmrcMembers: any;
    public AllCmrcActiveMemberModel: any;
    public showActiveMembers: boolean;
    public showWithdrawlMembers: boolean;
    public status: number;
    constructor(public _cmrcservice: CMRCService,
        public _router: Router,
        public _access_token: AccessToken,
        public _globalURL: GlobalAssets,
        public exportToExcel: ExportToExcel,
        public exportToPDF: ExportToPDF
    ) {
        _globalURL.isLoading = true;

        if (sessionStorage.getItem("access_token") == ""){
            _router.navigate(['../login']);
        }
    }
    ngOnInit() {
        this.status = 2;
        this.getActiveOrWithdrawalShg();     
    }
    getActiveOrWithdrawalShg(): void {     
        debugger;
      //  this.status = status;
        this._globalURL.isLoading = true;
      //  this.ShgsByCmrc = null;
        this._cmrcservice.get(this._globalURL.GET_SHG_BYCMRC, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.ShgsByCmrc = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    setInterval(() => {
                        this._globalURL.isLoading = false;
                    }, 5000);
                }
            });
    }

    public activeOrWithdrawalShg(){
        this._globalURL.isLoading = true;
        this.exportToPDF.activeOrWithdrawalShg(this.ShgsByCmrc);
        this._globalURL.isLoading = false;
    }
}
























