import { Component, AfterViewInit, OnInit, ViewChild } from '@angular/core';
import { CurrencyPipe } from '@angular/common';
import { VOService } from '../../../services/vo.services';
import { GlobalAssets } from '../../../environments/Global';
import { AccessToken } from '../../../environments/access_token';
import { Router } from '@angular/router';
import { FileUploadService } from '../../../services/FileService/file_upload.service';
import { CommonService } from '../../../services/common.service';
import { LangulageConf } from '../../../environments/language.config';
import { DataTableDirective } from 'angular-datatables';
import { Subject } from 'rxjs';
import { DatepickerOptions } from 'ng2-datepicker';
import * as frLocale from 'date-fns/locale/en';
import { CommonMessageComponent } from '../../../environments/CommanMssage';
import { NgForm } from '@angular/forms';
import { Title } from '../../../../../node_modules/@angular/platform-browser';
import { TitleConstants } from '../../../utils/constants/title-constants';

@Component({
    templateUrl: '../income/other_income.component.html'
})

export class VOOtherIncome implements OnInit, AfterViewInit {
    public changePhotoDetails: any;
    public showWarningMessage: boolean;
    public showErrorMessage: boolean;
    public todaysOrToDt: string;
    public commonService: any;
    public PaidDate: Date = new Date();
    public showSuccMessage: boolean;
    @ViewChild(DataTableDirective)
    dtElement: DataTableDirective;
    dtInstance: DataTables.Api;
    public dtTrigger: Subject<any> = new Subject();
    options: DatepickerOptions = {
        minYear: 2000,
        maxYear: (new Date().getFullYear() + 1),
        displayFormat: 'MMM D[,] YYYY',
        barTitleFormat: 'MMMM YYYY',
        dayNamesFormat: 'dd',
        firstCalendarDay: 0, // 0 - Sunday, 1 - Monday
        locale: frLocale,
        //minDate: new Date(Date.now()), // Minimal selectable date
        //minDate: new Date("2016-03-03"),
        //maxDate: new Date(Date.now()),  // Maximal selectable date
        barTitleIfEmpty: 'Click to select a date'
    };
    public deleterecord: any;
    ShowErrormsg: boolean;
    public SHGNameAndId: any;
    public membersNameAndId: any;
    public selectedShgId: any;
    public showMessage: any;
    public message: any;
    public convertedWordsInEnglish: string;
    public convertedWordsInMarathi: string;
    public convertedWordsInHindi: string;
    public otherIncomeDetails: any;
    public totalDonationAmt: number;
    constructor(public _voService: VOService,
        public _globalURL: GlobalAssets,
        public _commonService: CommonService,
        public _access_token: AccessToken,
        public _router: Router,
        public lang: LangulageConf,
        private _fileUploadService: FileUploadService,
        public _common_message: CommonMessageComponent,
        public titleService: Title) {
        this.selectedShgId = null;
        if (sessionStorage.getItem("access_token") == "")
            this._router.navigate(['../login']);
        this.totalDonationAmt = 0;
        this.convertedWordsInEnglish = "";
        this.convertedWordsInHindi = "";
        this.convertedWordsInMarathi = "";
        this.setTitle(TitleConstants.VO_OTHER_INCOME);
    }

    ngOnInit() {
        this.getOtherIncomeDetails();
    }

    ngAfterViewInit(): void {
        this.dtTrigger.next();
        //this.getOtherIncomeDetails();
    }
    public setTitle(newTitle: string) {
        this.titleService.setTitle(newTitle);
    }
    getOtherIncomeDetails() {
        this._globalURL.isLoading = true;
        this.totalDonationAmt = 0;
        this._voService.get(this._globalURL.GET_OTHER_INCOME_DETAILS, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this._router.navigate(['../login']);
                }
                debugger;
                if (res.status == 200) {
                    if (res.status == 200) {
                        this.otherIncomeDetails = JSON.parse(res._body).Data;
                        for (var item in this.otherIncomeDetails) {
                            this.totalDonationAmt = this.totalDonationAmt + this.otherIncomeDetails[item].Amount;
                            this._globalURL.isLoading = false;
                        }
                    }
                    else {
                        this.showWarningMessage = true;
                        this.message = this._common_message.WarningMsg;
                        setInterval(() => {
                            this.showWarningMessage = false;
                        }, 5000);
                        this._globalURL.isLoading = false;
                    }
                }
                else {
                    if (res.Status == true) {
                        this.otherIncomeDetails = res.Data;
                        for (var item in this.otherIncomeDetails)
                            this.totalDonationAmt = this.totalDonationAmt + this.otherIncomeDetails[item].Amount;
                        this.ShowErrormsg = false;
                        this._globalURL.isLoading = false;
                    }
                    else {
                        this.showWarningMessage = true;
                        this.message = this._common_message.WarningMsg;
                        setInterval(() => {
                            this.showWarningMessage = false;
                        }, 5000);
                        this._globalURL.isLoading = false;
                    }
                }
                this.reRenderDT();
            }).catch((res) => {
                this.showErrorMessage = true;
                this.message = this._common_message.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this._globalURL.isLoading = false;
            })
    }

    public ngFormData: NgForm;
    createIncome(data: NgForm, metadata: any) {
        debugger;
        this.ngFormData = data;
        this._globalURL.isLoading = true;
        //data.value.PaidDate = this._commonService.convertToDate(data.value.PaidDate);
        this._voService.post(this._globalURL.ADD_OTHER_INCOME, data.value, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == undefined) {
                    if (res.status == 200) {
                        var data = JSON.parse(res._body);
                        var bankFile = metadata.form[7].files[0];
                        var bankFileFormData = new FormData();
                        bankFileFormData.append("file", bankFile);
                        bankFileFormData.append("RefId", data.Data);
                        this._fileUploadService.post((this._globalURL.ADD_PHOTO + "?folderName=OtherIncomeReceipt" + "&type=" + this._globalURL.OTHER_INCOME_BANK_RECEIPT + "&RefId=" + data.Data + "&PhotoId=0"), bankFileFormData, sessionStorage.getItem("access_token"))
                            .then((res) => {
                                debugger;
                                if (res.Status == undefined) {
                                    if (res.status == 200) {
                                        this.getOtherIncomeDetails();
                                    }
                                    else {
                                        this.showWarningMessage = true;
                                        this.message = this._common_message.WarningMsg;
                                        setInterval(() => {
                                            this.showWarningMessage = false;
                                        }, 5000);
                                        this._globalURL.isLoading = false;
                                    }
                                }
                                else {
                                    if (res.Status == true) {
                                        this.getOtherIncomeDetails();
                                    }
                                    else {
                                        this.showWarningMessage = true;
                                        this.message = this._common_message.WarningMsg;
                                        setInterval(() => {
                                            this.showWarningMessage = false;
                                        }, 5000);
                                        this._globalURL.isLoading = false;
                                    }
                                }
                            }).catch((res) => {
                                this.showErrorMessage = true;
                                this.message = this._common_message.CatchBlockMsg;
                                setInterval(() => {
                                    this.showErrorMessage = false;
                                }, 5000);
                                this._globalURL.isLoading = false;
                            })
                        debugger;
                        var checkFile = metadata.form[8].files[0];
                        var checkFileFormData = new FormData();
                        checkFileFormData.append("file", checkFile);
                        checkFileFormData.append("RefId", data.Data);
                        this._fileUploadService.post((this._globalURL.ADD_PHOTO + "?folderName=OtherIncomeReceipt" + "&type=" + this._globalURL.OTHER_INCOME_CHECK_RECEIPT + "&RefId=" + data.Data + "&PhotoId=0"), checkFileFormData, sessionStorage.getItem("access_token"))
                            .then((res) => {
                                if (res.Status == undefined) {
                                    if (res.status == 200) {
                                        this.ngFormData.control.reset();
                                        this.getOtherIncomeDetails();
                                    }
                                    else {
                                        this.showWarningMessage = true;
                                        this.message = this._common_message.WarningMsg;
                                        setInterval(() => {
                                            this.showWarningMessage = false;
                                        }, 5000);
                                        this._globalURL.isLoading = false;
                                    }
                                }
                                else {
                                    if (res.Status == true) {
                                        this.getOtherIncomeDetails();
                                    }
                                    else {
                                        this.showWarningMessage = true;
                                        this.message = this._common_message.WarningMsg;
                                        setInterval(() => {
                                            this.showWarningMessage = false;
                                        }, 5000);
                                        this._globalURL.isLoading = false;
                                    }
                                }
                            }).catch((res) => {
                                this.showErrorMessage = true;
                                this.message = this._common_message.CatchBlockMsg;
                                setInterval(() => {
                                    this.showErrorMessage = false;
                                }, 5000);
                                this._globalURL.isLoading = false;
                            })
                    }
                }
                this._globalURL.isLoading = false;
                this.showMessage = true;
                this.message = this._common_message.AddOtherIncomeMsg;
                setInterval(() => {
                    this.showMessage = false;
                }, 5000);
            }).catch((err) => {
                this.showErrorMessage = true;
                this.message = this._common_message.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this._globalURL.isLoading = false;
            })
    }

    convertToWords(num) {
        this.convertedWordsInEnglish = this._commonService.convertDigitToWordsInEnglish(num);
        this.convertedWordsInHindi = this._commonService.convertDigitToWordsInHindi(num);
        this.convertedWordsInMarathi = this._commonService.convertDigitToWordsInMarathi(num);
    }

    fordelete(data: any) {
        this.deleterecord = data;
    }
    deleteOtherIncome(id: number) {
        this._voService.get(this._globalURL.DELETE_OTHER_INCOME_DETAILS + id, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this._router.navigate(['../login']);
                }

                if (res.Status == true) {
                    this._globalURL.isLoading = false;
                    this.message = this._common_message.DeleteOtherIncomeMsg;
                    this.showMessage = true;
                    setInterval(() => {
                        this.showMessage = false;
                    }, 5000);
                    this.getOtherIncomeDetails();
                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._common_message.WarningMsg;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this.getOtherIncomeDetails();
                    this._globalURL.isLoading = false;
                }
            }).catch((res) => {
                this.showErrorMessage = true;
                this.message = this._common_message.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this.getOtherIncomeDetails();
                this._globalURL.isLoading = false;
            })
    }

    forchange(data: any) {
        this.changePhotoDetails = data;
    }


    public chnageBankPhoto(metadata: any, refId: string, phtoId?: number) {
        debugger;
        var formData = new FormData();
        formData.append("RefId", refId);
        formData.append("file", metadata);
        this._fileUploadService.post((this._globalURL.ADD_PHOTO + "?folderName=OtherIncomeReceipt" + "&type=" + this._globalURL.OTHER_INCOME_BANK_RECEIPT + "&RefId=" + refId + "&PhotoId=" + (phtoId == 0 || phtoId == null ? 0 : phtoId)),
            formData, sessionStorage.getItem("access_token")).then((res) => {
                if (res.Status == undefined) {
                    if (res.status == 200) {
                        this.getOtherIncomeDetails();
                        this.showMessage = true;
                        this.message = this._common_message.PhotoChangeMsg;
                        setInterval(() => {
                            this.showMessage = false;
                        }, 5000);
                    }
                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._common_message.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                }
            }).catch((res) => {
                this.showErrorMessage = true;
                console.log(res.error);
                this.message = this._common_message.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this._globalURL.isLoading = false;
            })

    }


    public chnageChequePhoto(metadata: any, refId: string, phtoId?: number) {
        debugger;
        var formData = new FormData();
        formData.append("RefId", refId);
        formData.append("file", metadata);
        this._fileUploadService.post((this._globalURL.ADD_PHOTO + "?folderName=OtherIncomeReceipt" + "&type=" + this._globalURL.OTHER_INCOME_CHECK_RECEIPT + "&RefId=" + refId + "&PhotoId=" + (phtoId == 0 || phtoId == null ? 0 : phtoId)),
            formData, sessionStorage.getItem("access_token")).then((res) => {
                if (res.Status == undefined) {
                    if (res.status == 200) {
                        this.getOtherIncomeDetails();
                        this.showMessage = true;
                        this.message = this._common_message.PhotoChangeMsg;
                        setInterval(() => {
                            this.showMessage = false;
                        }, 5000);
                    }
                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._common_message.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                }
            }).catch((res) => {
                this.showErrorMessage = true;
                console.log(res.error);
                this.message = this._common_message.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this._globalURL.isLoading = false;
            })

    }


    reRenderDT() {
        this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
            dtInstance.destroy();
            this.dtTrigger.next();
        });
    }
}


