import { Component, OnInit, ViewChild, AfterViewInit } from '@angular/core';
import { constructor } from 'events';
import { Router } from '@angular/router';
import { AccessToken } from '../../../environments/access_token';
import { GlobalAssets } from '../../../environments/Global';
import { CMRCService } from '../../../services/cmrc.services';
import { CommonMessageComponent } from "../../../environments/CommanMssage";
import { GlobalVariable } from '../../../environments/GlobalVeriables';
import { VoDetailsModel } from './vodetails.model';
import { NgForm } from '../../../../../node_modules/@angular/forms';
import { DataTableDirective } from '../../../../../node_modules/angular-datatables';
import { Subject } from '../../../../../node_modules/rxjs';
import { Title } from '../../../../../node_modules/@angular/platform-browser';
import { TitleConstants } from '../../../utils/constants/title-constants';

@Component({
    templateUrl: '../vodetail/vodetails.component.html',
    styleUrls: ['../../../components/app.css']

})

export class VoDetailsComponent {
    public showWarningMessage: boolean;
    public areas: any;
    public Cmrcs: any;
    public viewData: any;
    public CmrcDetailas: any;
    public _common_message: any;
    public showErrorMessage: boolean;
    public showMessage: boolean;
    public isCmrcDetail: boolean = true;
    public message: any;
    public isCMRCDetails: boolean = false;
    public vodata: any;
    public showSucessMessage: boolean;
    @ViewChild(DataTableDirective)
    dtElement: DataTableDirective;
    dtInstance: DataTables.Api;
    public dtTrigger: Subject<any> = new Subject();
    constructor(public _cmrcservice: CMRCService,
        public _router: Router,
        public vodetailsmodel: VoDetailsModel,
        public _globalURL: GlobalAssets,
        public globalVariable: GlobalVariable,
        public _commonMessage: CommonMessageComponent,
        public titleService: Title
    ) {
        _globalURL.isLoading = true;
        if (sessionStorage.getItem("access_token") == "") {
            _router.navigate(['../login']);
        }
        this.setTitle(TitleConstants.DISTRICT_VO_DETAILS);
    }
    ngOnInit() {
        this.getallCmrc();
        this.getAreaKVP()
    }

    ngAfterViewInit(): void {
        this.dtTrigger.next();
    }

    public setTitle(newTitle: string) {
        this.titleService.setTitle(newTitle);
    }
    getallCmrc(): void {
        this._globalURL.isLoading = true;
        this._cmrcservice.get(this._globalURL.GET_KVP_DISTRICT_CMRC_DASHBOARD, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.Cmrcs = res.Data;
                    this.isCMRCDetails = true;
                    this._globalURL.isLoading = false;
                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._commonMessage.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                }
            }).catch((res) => {
                this.showErrorMessage = true;
                this.message = this._commonMessage.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this._globalURL.isLoading = false;
            })
    }
    getvoDetails(id: number): void {
        debugger;
        this.vodata = null;
        this._globalURL.isLoading = true;
        this._cmrcservice.get(this._globalURL.GET_VODETAILS_BY_CMRCID + id, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.vodata = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._commonMessage.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                }
            }).catch((res) => {
                this.showErrorMessage = true;
                this.message = this._commonMessage.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this._globalURL.isLoading = false;
            })
    }

    viewVODetails(data: any): void {
        this.viewData = data;
    }

    forUpdate(data: any) { 
        this.vodetailsmodel = data;
    }
    data: any;
    updateVoDetail(data: VoDetailsModel) {
        debugger;
        this._globalURL.isLoading = true; 
        this._cmrcservice.post(this._globalURL.UPDATE_VO_DETAILS, data, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == undefined) {
                    if (res.status == 200) {                       
                        this._globalURL.isLoading = false;                 
                        this.message = this._commonMessage.UpdateMsg;
                        this.showSucessMessage = true;
                        setInterval(() => {
                            this.showSucessMessage = false;
                        }, 5000);
                    }
                }
                else {
                    this._globalURL.isLoading = false;
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                }
            }).catch((res) => {
                console.log(res.error);
                this._globalURL.isLoading = false;
                this.showErrorMessage = true;
                this.message = this._common_message.error401message;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            });
    }
    getAreaKVP(): void {
        this._globalURL.isLoading = true;
        this._cmrcservice.get(this._globalURL.GET_AREA_KVP, sessionStorage.getItem("access_token"))
            .then((res) => {               
                if (res.Status == true) {
                    this.areas = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._common_message.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                }
            }).catch((err) => {
                this._globalURL.isLoading = false;
                this.message = this._common_message.CatchBlockMsg;
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            });
    }

    reRenderDT() {
        this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
            dtInstance.destroy();
            this.dtTrigger.next();
        });
    }
}


