import { Injectable } from "@angular/core";

@Injectable()
export class CmrcAdminModel {

    public Id: number;
    public City: string;
    public CMRCName: string;  
    public Address: any;
    public District: string;
    public FormationDate: any;
    public Taluka: any;
    public BankAccountNumber:any;
    public BankAddress: any;
    public BankName: any;
    public IFSCCode: any;
    public BranchName: any;
    public AreaId: any;

    constructor() {
        this.Id = 0;
        this.City = "";
        this.CMRCName = '';
        this.FormationDate = '';
        this.Address = '';
        this.District = '';
        this.Taluka = '';
        this.BankAccountNumber = '';
        this.BankAddress = '';
        this.BankName = '';
        this.IFSCCode = '';
        this.BranchName = '';
        this.AreaId = '';
    }

} 





