import { Component, ViewChild } from "@angular/core";
import { FileUploadService } from "../../../services/FileService/file_upload.service";
import { AccessToken } from "../../../environments/access_token";
import { DISTRICTService } from "../../../services/district.services";
import { GlobalAssets } from "../../../environments/Global";
import { CommonService } from "../../../services/common.service";
import { ExportToPDF } from "../../../services/reports/exportpdf";
import { ExportToExcel } from "../../../services/reports/exportexcel";
import { Router } from "@angular/router";
import { LangulageConf } from "../../../environments/language.config";
import { GlobalVariable } from "../../../environments/GlobalVeriables";
import { LoanMasterModel } from "./loan.model.component";
import { CommonMessageComponent } from "../../../environments/CommanMssage";
import { Subject } from "../../../../../node_modules/rxjs";
import { DataTableDirective } from "../../../../../node_modules/angular-datatables";
import { Title } from "../../../../../node_modules/@angular/platform-browser";
import { TitleConstants } from "../../../utils/constants/title-constants";

@Component({
    templateUrl: '../loan/loan.component.html',
    styleUrls: ['../../../components/app.css']
})


export class DistrictLoanComponent {
    public disable: boolean;
    public calculateButtonValue: string;
    public totalLoanAmt: number;
    public totalPayable: number;
    public totalPrinciple: number;
    public totalLoanApprovedAmount: number;
    public memLoanRepaymentDetails: any;
    public cmrcId: any;
    public voId: any;
    selectedStatus(arg0: any, arg1: any): any {
        throw new Error("Method not implemented.");
    }
    public Vos: any;
    public Cmrcs: any;
    public memberLoanDetails: any;
    public selectedLoanStatus: number;
    public message: string;
    public showMessage: boolean;
    public showErrorMessage: boolean;
    public showWarningMessage: boolean;
    public convertedWordsInEnglish: string;
    public convertedWordsInMarathi: string;
    public convertedWordsInHindi: string;
    public DeleteLoanData: any;
    public selectedCmrc: any;
    public selectedVo: any;
    constructor(public _districtService: DISTRICTService,
        public _access_token: AccessToken,
        public _globalURL: GlobalAssets,
        public _commonService: CommonService,
        public exportToPdf: ExportToPDF,
        public exportToExcel: ExportToExcel,
        public router: Router,
        public lang: LangulageConf,
        public globalVariable: GlobalVariable,
        public _commonMessage: CommonMessageComponent,
        public LoanData: LoanMasterModel,
        public titleService: Title) {
        this.selectedLoanStatus = globalVariable.ACTIVE_SHG;
        this.setTitle(TitleConstants.DISTRICT_LOAN_DETAILS);
        if (sessionStorage.getItem("goTOLoan") == "true") {
            sessionStorage.setItem("goTOLoan",null);
            this.loanSHGLoanDataByStaus(sessionStorage.getItem("setVoId"), globalVariable.ACTIVE_SHG);
            this.cmrcId = sessionStorage.getItem("cmrcId");
            this.voId = parseInt(sessionStorage.getItem("voId"));
        }

    }

    ngOnInit() {
        this.getCMRCsKVP();
    }

    public setTitle(newTitle: string) {
        this.titleService.setTitle(newTitle);
    }
    getVoByCmrcId(selectCmrc: any): void {
        this.selectedCmrc = selectCmrc;
        sessionStorage.setItem("cmrcId", selectCmrc);
        this._globalURL.isLoading = true;
        this._districtService.get((this._globalURL.GET_VO_BY_CMRC_ID + "?cmrcId=" + selectCmrc), sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.Vos = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._commonMessage.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                }
            }).catch((res) => {
                this.showErrorMessage = true;
                this.message = this._commonMessage.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this._globalURL.isLoading = false;
            })
    }

    getCMRCsKVP(): void {
        debugger;
        this._globalURL.isLoading = true;
        this._districtService.get(this._globalURL.GET_KVP_DISTRICT_CMRC_DASHBOARD, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.Cmrcs = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._commonMessage.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                }
            }).catch((res) => {
                this.showErrorMessage = true;
                this.message = this._commonMessage.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this._globalURL.isLoading = false;
            })
    }

    forLoanSHGLoanDataByStaus(id: number) {
        this.loanSHGLoanDataByStaus(this.selectedVo, id);
    }
    loanSHGLoanDataByStaus(voId: any, loanStatus: number) {
        sessionStorage.setItem("voId", voId);
        this.selectedVo = voId
        this.selectedLoanStatus = loanStatus;
        this._globalURL.isLoading = true;
        this._districtService.get((this._globalURL.GET_DISTRICT_LOAN_DETAILS + "?voId=" + voId + "&status=" + loanStatus), sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.memberLoanDetails = res.Data;
                    this._globalURL.isLoading = false;
                    //  this. reRenderDT() ;
                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._commonMessage.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                }
            }).catch((res) => {
                this.showErrorMessage = true;
                this.message = this._commonMessage.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this._globalURL.isLoading = false;
            })
    }


    public setUpdate(loanDetail: any) {
        this.convertToWords(loanDetail.LoanApprovedAmount); debugger;
        this.LoanData = loanDetail;
    }

    naviagetToRepayment(memid: any, loanAmt: any) {
        this._globalURL.selectedLoanId = memid;
        this._globalURL.loanAmount = loanAmt;
        this.router.navigate(['/district/UpdateLoanRepayment']);
        sessionStorage.setItem("setVoId", this.selectedVo)
    }
    calculateRepayment(data: any) {
        debugger;
        this.disable = true;
        this.calculateButtonValue = "calculating ...";

        //this.memLoanRepaymentDetails = null;
        this._globalURL.isLoading = true;
        this.totalLoanAmt = 0;
        this.totalPayable = 0;
        this.totalPrinciple = 0;
        this.totalLoanApprovedAmount = 0;
        this._districtService.get(
            (this._globalURL.CALCULATE_REPAYMENT + "?amount=" + data.LoanApprovedAmount +
                "&interest=" + data.interest + "&tenure=" + data.tenure + "&effectiveDate=" + data.effectiveDate),
            sessionStorage.getItem("access_token"))
            .then((res) => {
                this.memLoanRepaymentDetails = res.Data;
                for (var item in res.Data) {
                    this.totalLoanAmt = this.totalLoanAmt + res.Data[item].FixedPayableAmt;
                    this.totalPayable = this.totalPayable + res.Data[item].TotalPayableAmt;
                    this.totalPrinciple = this.totalPrinciple + res.Data[item].InterestAmt;
                    this.totalLoanApprovedAmount = this.totalLoanApprovedAmount + res.Data[item].LoanApprovedAmount;
                }
                this._globalURL.isLoading = false;
            })
    }
    public confirmUpdate() {
        this._globalURL.isLoading = true;
        let data = this.LoanData;
        this._districtService.post(this._globalURL.UPDATE_DISTRICT_LOAN_DETAILS, data, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 200) {
                    this._globalURL.isLoading = false;
                    this.message = this._commonMessage.UpdateMsg;
                    this.showMessage = true;
                    setInterval(() => {
                        this.showMessage = false;
                    }, 5000);
                    this.loanSHGLoanDataByStaus(this.selectedVo, this.selectedLoanStatus);
                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._commonMessage.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                }
            }).catch((res) => {
                this.showErrorMessage = true;
                this.message = this._commonMessage.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this._globalURL.isLoading = false;
            })
    }

    convertToWords(num) {
        this.convertedWordsInEnglish = this._commonService.convertDigitToWordsInEnglish(num);
        this.convertedWordsInHindi = this._commonService.convertDigitToWordsInHindi(num);
        this.convertedWordsInMarathi = this._commonService.convertDigitToWordsInMarathi(num);
    }

    public deleteLoanDetails(data: any): void {
        debugger;
        this.DeleteLoanData = data;
    }

    public confirmDelete(): void {
        debugger;
        let data = this.DeleteLoanData;
        this._districtService.post(this._globalURL.DELETE_LOAN_DETAILS + data.LoanId, null, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 200) {
                    this._globalURL.isLoading = false;
                    this.message = this._commonMessage.DeleteMsg;
                    this.showMessage = true;
                    setInterval(() => {
                        this.showMessage = false;
                    }, 5000);
                    this.loanSHGLoanDataByStaus(this.selectedVo, this.selectedLoanStatus);
                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._commonMessage.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                }
            }).catch((res) => {
                this.showErrorMessage = true;
                this.message = this._commonMessage.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this._globalURL.isLoading = false;
            })
    }
}


