    public getBalancesheet(frDt: any, toDt: any, conf: number) {
        this._globalURL.isLoading = true;
        if (conf == 2) {
            frDt = this.lastFinanceYearOrFromDt = "1 APR" + (new Date().getFullYear());
            toDt = this.todaysOrToDt = this.commonService.getTodaysDate();
            this.balanceSheetFromDt = new Date();
            this.balanceSheetToDt = new Date();
        }
        if (conf == 1) {
            this.lastFinanceYearOrFromDt = frDt.displayValue;
            this.todaysOrToDt = toDt.displayValue;
            frDt = this.commonService.convertToDate(frDt);
            toDt = this.commonService.convertToDate(toDt);
        }
        let balFinalUrl = "";

        if (sessionStorage.getItem("roleId") == "2") {
            this._globalURL.get_GET_BALANCESHEET = "";
            balFinalUrl = this._globalURL.GET_BALANCESHEET + (sessionStorage.getItem("roleId") == null ? "2" : sessionStorage.getItem("roleId")) + "&frDt=" + frDt + "&toDt=" + toDt;
        }
        else
            balFinalUrl = this._globalURL.GET_BALANCESHEET + "&frDt=" + frDt + "&toDt=" + toDt;
        this.service.get(balFinalUrl, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this.router.navigate(['../login']);
                }
                if (res.Status == undefined)
                    if (res.status == 200) {
                        this.balancesheetVO = JSON.parse(res._body).Data;
                    }
                    else {
                        this.showWarningMessage = true;
                        this.message = this._common_message.WarningMsg;
                        setInterval(() => {
                            this.showWarningMessage = false;
                        }, 5000);
                        this._globalURL.isLoading = false;
                    }
                else {
                    if (res.Status == true) {
                        this.balancesheetVO = res.Data;
                    }
                    else {
                        this.showWarningMessage = true;
                        this.message = this._common_message.WarningMsg;
                        setInterval(() => {
                            this.showWarningMessage = false;
                        }, 5000);
                        this._globalURL.isLoading = false;
                    }
                }
                this._globalURL.isLoading = false;
            }).catch((res) => {
                this._globalURL.isLoading = false;
                this.message = this._common_message.CatchBlockMsg;
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            })
    }

