import { Component, OnInit, ViewChild, AfterViewInit } from '@angular/core';
import { CurrencyPipe } from '@angular/common';
import { VOService } from '../../../services/vo.services';
import { Router } from '@angular/router';
import { AccessToken } from '../../../environments/access_token';
import { GlobalAssets } from '../../../environments/Global';
import { CMRCService } from '../../../services/cmrc.services';
import { CommonService } from '../../../services/common.service';
import { DataTableDirective } from 'angular-datatables';
import { GlobalVariable } from '../../../environments/GlobalVeriables';
import { Subject } from 'rxjs';
import { LangulageConf } from '../../../environments/language.config';
import { CommonMessageComponent } from '../../../environments/CommanMssage';
import { NgForm } from '@angular/forms';
import { FileUploadService } from '../../../services/FileService/file_upload.service';
import { Title } from '../../../../../node_modules/@angular/platform-browser';
import { TitleConstants } from '../../../utils/constants/title-constants';

@Component({
    templateUrl: '../fund/fund.component.html',
    styleUrls: ['../../../components/app.css'],
})

export class VOFundComponent implements OnInit, AfterViewInit {
    public changePhotoDetail: any;
    public showWarningMessage: boolean;
    public showMessage: boolean;
    public showErrorMessage: boolean;
    public message: string;
    public ShowErrormsg: boolean;
    public fundgroups: any;
    public fundDetails: any;
    public totalRequestAmt: any;
    public totalApprovedAmt: any;
    public fundTypeId: number;
    public RequestAmount: string;
    public convertedWordsInEnglish: string;
    public convertedWordsInMarathi: string;
    public convertedWordsInHindi: string;
    // @ViewChild(DataTableDirective)
    // dtElement: DataTableDirective;
    // dtInstance: DataTables.Api;
    // public dtTrigger: Subject<any> = new Subject();
    constructor(public _cmrcService: CMRCService,
        public _voservice: VOService,
        public _globalURL: GlobalAssets,
        public _access_token: AccessToken,
        public _router: Router,
        public lang: LangulageConf,
        public _commonService: CommonService,
        public _globalVariable: GlobalVariable,
        public _common_message: CommonMessageComponent,
        public router: Router,
        private _fileUploadService: FileUploadService,
        public titleService: Title) {
        if (sessionStorage.getItem("access_token") == "")
            router.navigate(['../login']);
        this.fundDetails = null;
        this.totalRequestAmt = null;
        this.totalApprovedAmt = null;
        this.fundTypeId = this._globalVariable.NEW_FUND;
        this.RequestAmount = "";
        this.convertedWordsInEnglish = "";
        this.convertedWordsInHindi = "";
        this.convertedWordsInMarathi = "";
        this.setTitle(TitleConstants.VO_FUND_DETAILS);
    }
    ngOnInit(): void {
        this.getFund(this.fundTypeId);
    }
    ngAfterViewInit(): void {
       // this.dtTrigger.next();
    }
    public setTitle(newTitle: string) {
        this.titleService.setTitle(newTitle);
    }

    getFund(id: number): void {
        debugger;
        this.fundTypeId = id;
        this._globalURL.isLoading = true;
        this.fundDetails = null;
        this.totalRequestAmt = 0;
        this.totalApprovedAmt = 0;
        this._voservice.get(this._globalURL.GET_ALL_VO_FUND_DETAILS_FOR_VO + id, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this.router.navigate(['../login']);
                }
                if (res.Status == true) {
                    for (var item in res.Data) {
                        this.totalRequestAmt = this.totalRequestAmt + res.Data[item].RequestAmount;
                        this.totalApprovedAmt = this.totalApprovedAmt + res.Data[item].ApproveAmount;
                    }
                    this.fundDetails = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this._globalURL.isLoading = false;
                }
               // this.reRenderDT();
            })
            .catch((err) => {
                this.showErrorMessage = true;
                this.message = this._common_message.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this._globalURL.isLoading = false;
            })
    }

    public formdata: NgForm;
    SaveFundRequest(data: NgForm, metadata: any) {
        debugger;
        this.formdata = data;
        this._globalURL.isLoading = true;
        this._voservice.post(this._globalURL.REQUEST_FUND_FROM_CRMC, data.value, sessionStorage.getItem("access_token"))
            .then((res) => {
                debugger;
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this.router.navigate(['../login']);
                }
                if (res.Status == undefined) {
                    if (res.status === 200) {
                        this.formdata.control.reset();
                        this._globalURL.isLoading = false;
                        let data = JSON.parse(res._body);
                        var formData = new FormData();
                        formData.append("RefId", data.Data);
                        formData.append("file", metadata);
                        this._fileUploadService.post((this._globalURL.ADD_PHOTO + "?folderName=Fund" + "&type=" + this._globalURL.VO_TO_CMRC_FUND_APPLICATION_FORM + "&RefId=" + data.Data + "&PhotoId=0"),
                            formData, sessionStorage.getItem("access_token")).then((res) => {
                                this.getFund(this._globalVariable.NEW_FUND);
                                if (res.status === 401) {
                                    sessionStorage.clear();
                                    this.showErrorMessage = true;
                                    this.message = this._common_message.error401message;
                                    setInterval(() => {
                                        this.showErrorMessage = false;
                                    }, 5000);
                                    this._globalURL.isLoading = false;
                                    this.router.navigate(['../login']);
                                }
                                if (res.Status == undefined) {
                                    if (res.status == 200) {
                                        this.message = this._common_message.FundRequestMsg;
                                        this.showMessage = true;
                                        setInterval(() => {
                                            this.showMessage = false;
                                        }, 5000);
                                    }
                                }
                                else {
                                    this.showErrorMessage = true;
                                    this.message = this._common_message.CatchBlockMsg;
                                    setInterval(() => {
                                        this.showErrorMessage = false;
                                    }, 5000);
                                    this._globalURL.isLoading = false;
                                }
                            }).catch((res) => {
                                this.showErrorMessage = true;
                                this.message = this._common_message.CatchBlockMsg;
                                setInterval(() => {
                                    this.showErrorMessage = false;
                                }, 5000);
                                this._globalURL.isLoading = false;
                            })
                    }
                    else {
                        this.message = this._common_message.FundRequestErrorMsg;
                        this.showErrorMessage = true;
                        setInterval(() => {
                            this.showErrorMessage = false;
                        }, 5000);
                    }
                }
                else {
                    if (res.Status == true) {
                        this._globalURL.isLoading = false;
                        this.getFund(this._globalVariable.NEW_FUND);
                        data.control.reset();
                    }
                    else {
                        this._globalURL.isLoading = false;
                        this.message = this._common_message.FundRequestErrorMsg;
                        this.showErrorMessage = true;
                        setInterval(() => {
                            this.showErrorMessage = false;
                        }, 5000);
                    }
                }
            })
            .catch((err) => {
                this.showErrorMessage = true;
                this.message = this._common_message.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this._globalURL.isLoading = false;
            })
    }

    convertToWords(num) {
        this.convertedWordsInEnglish = this._commonService.convertDigitToWordsInEnglish(num);
        this.convertedWordsInHindi = this._commonService.convertDigitToWordsInHindi(num);
        this.convertedWordsInMarathi = this._commonService.convertDigitToWordsInMarathi(num);
    }

    navigateToFundRepayment() {
        //this._globalVariable.SelectedFundId = id;
        this._router.navigate(['/vo/fundrepayment']);
    }

    public chnagePhoto(metadata: any, refId: string, phtoId?: number) {
        debugger;
        var formData = new FormData();
        formData.append("RefId", refId);
        formData.append("file", metadata);
        this._fileUploadService.post((this._globalURL.ADD_PHOTO + "?folderName=Fund" + "&type=" + this._globalURL.VO_TO_CMRC_FUND_APPLICATION_FORM + "&RefId=" + refId + "&PhotoId=" + (phtoId == 0 || phtoId == null ? 0 : phtoId)),
            formData, sessionStorage.getItem("access_token")).then((res) => {
                if (res.Status == undefined) {
                    if (res.status == 200) {
                        this.getFund(this._globalVariable.NEW_FUND);
                        this.showMessage = true;
                        this.message = this._common_message.PhotoChangeMsg;
                        setInterval(() => {
                            this.showMessage = false;
                        }, 5000);
                    }
                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._common_message.WarningMsg;
                    this.message = this._common_message.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                }
            }).catch((res) => {
                this.showErrorMessage = true;
                console.log(res.error);
                this.message = this._common_message.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this._globalURL.isLoading = false;
            })
            
    }

    forchange(data:any){
        this.changePhotoDetail = data;
    }
    // reRenderDT() {
    //     this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
    //         dtInstance.destroy();
    //         this.dtTrigger.next();
    //     });
    // }
}


