import { Component, OnInit, ViewChild, AfterViewInit } from '@angular/core';
import { CurrencyPipe } from '@angular/common';
import { CMRCService } from '../../../services/cmrc.services';
import { AccessToken } from '../../../environments/access_token';
import { GlobalAssets } from '../../../environments/Global';
import { Router } from '@angular/router';
import { DataTableDirective } from 'angular-datatables';
import { Subject } from 'rxjs/Subject';
import { LangulageConf } from '../../../environments/language.config';
import { CommonMessageComponent } from '../../../environments/CommanMssage';
import { NgForm } from '@angular/forms';
import { CommonService } from '../../../services/common.service';
import { DatepickerOptions } from 'ng2-datepicker';
import * as frLocale from 'date-fns/locale/en';
import { Title } from '../../../../../node_modules/@angular/platform-browser';
import { TitleConstants } from '../../../utils/constants/title-constants';
import { GlobalVariable } from '../../../environments/GlobalVeriables';
@Component({
    templateUrl: 'ledgerentries.component.html'
})
export class CMRCSLEntryComponent implements OnInit {
    showWarningMessage: boolean;
    showMessage: boolean;
    @ViewChild(DataTableDirective)
    dtElement: DataTableDirective;
    dtInstance: DataTables.Api;
    public dtTrigger: Subject<any> = new Subject();
    public ToDt: Date = new Date();
    public FromDt: Date = new Date();
    options: DatepickerOptions = {
        minYear: 2000,
        maxYear: (new Date().getFullYear() + 1),
        displayFormat: 'MMM D[,] YYYY',
        barTitleFormat: 'MMMM YYYY',
        dayNamesFormat: 'dd',
        firstCalendarDay: 0, // 0 - Sunday, 1 - Monday
        locale: frLocale,
        barTitleIfEmpty: 'Click to select a date'
    };
    public lastFinanceYearOrFromDt: string;
    public todaysOrToDt: string;
    public slEntriesDetails: any;
    public showSuccMessage: boolean = false;
    public showErrorMessage: boolean = false;
    public message: any;
    public convertedWordsInEnglish: string;
    public convertedWordsInMarathi: string;
    public convertedWordsInHindi: string;
    constructor(
        public _CMRCService: CMRCService,
        public _globalURL: GlobalAssets,
        public lang: LangulageConf,
        public _commonService: CommonService,
        public accesstoken: AccessToken,
        public _router: Router,
        public _common_message: CommonMessageComponent,
        public titleService: Title,
        public globalVariables: GlobalVariable
    ) {
        if (sessionStorage.getItem("access_token") == "")
            _router.navigate(['../login']);
        this.convertedWordsInEnglish = "";
        this.convertedWordsInHindi = "";
        this.convertedWordsInMarathi = "";
        this.setTitle(TitleConstants.CMRC_LEDGER_ENTRIES);
    }

    public setTitle(newTitle: string) {
        this.titleService.setTitle(newTitle);
    }
    ngOnInit() {
        //this.getSLEntriesDetails();
    }
    ngAfterViewInit(): void {
        this.dtTrigger.next();
        this.getSLEntriesDetails(false);
    }
    getSLEntriesDetails(isAllEntries: boolean) {
        this._CMRCService.get(this._globalURL.GET_SL_ENTRIES_DETAILS + this.globalVariables.CMRCRoleId + "&isAllEntries=" + isAllEntries, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.slEntriesDetails = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._common_message.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                }
                 this.reRenderDT();
            }).catch((res) => {
                this._globalURL.isLoading = false;
                this.message = this._common_message.CatchBlockMsg;
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            })
    }

    loadSLDetails(frDt: any, toDt: any) {
        this.lastFinanceYearOrFromDt = this._commonService.convertToDate(frDt);
        this.todaysOrToDt = this._commonService.convertToDate(toDt);
        var urls = this._globalURL.GET_SL_ENTRIES_DETAILS + this.globalVariables.CMRCRoleId + "&fromdt=" + this.lastFinanceYearOrFromDt + "&todt=" + this.todaysOrToDt;
        this._CMRCService.get(this._globalURL.GET_SL_ENTRIES_DETAILS + 2 + "&fromdt=" + this.lastFinanceYearOrFromDt + "&todt=" + this.todaysOrToDt, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == undefined)
                    if (res.status == 200) {
                        if ((JSON.parse(res._body).Data.Status == 200))
                            this.slEntriesDetails = JSON.parse(res._body).Data;
                        else {
                            this.showWarningMessage = true;
                            this.message = this._common_message.WarningMsg;
                            setInterval(() => {
                                this.showWarningMessage = false;
                            }, 5000);
                            this._globalURL.isLoading = false;
                        }
                    }
                    else {
                        this.showWarningMessage = true;
                        this.message = this._common_message.WarningMsg;
                        setInterval(() => {
                            this.showWarningMessage = false;
                        }, 5000);
                        this._globalURL.isLoading = false;
                    }

                else {
                    if (res.Status == true) {
                        this.slEntriesDetails = res.Data;
                        this._globalURL.isLoading = false;
                    }
                    else {
                        this.showWarningMessage = true;
                        this.message = this._common_message.WarningMsg;
                        setInterval(() => {
                            this.showWarningMessage = false;
                        }, 5000);
                        this._globalURL.isLoading = false;
                    }
                }
            }).catch((res) => {
                this.showErrorMessage = true;
                console.log(res.error);
                this.message = this._common_message.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this._globalURL.isLoading = false;
            })
    }

    saveSLEntry(Data: any) {
        this._globalURL.isLoading = true;
        this._CMRCService.get((this._globalURL.SAVE_SL_ENTRY + "?slId=" + Data.slId + "&amount=" + Data.Amount + "&pmode=" + Data.pmode + "&TransactionDate=" + Data.TransactionDate + "&comment=" + Data.comment + "&shgId=null&memberId=null"), sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this._globalURL.isLoading = false;
                    this.showMessage = true;
                    this.message = this._common_message.SlEntryMsg;
                    setInterval(() => {
                        this.showMessage = false;
                    }, 5000);
                    //Data.control.reset();
                    this.getSLEntriesDetails(false);
                }
                else {
                    this._globalURL.isLoading = false;
                    this.message = this._common_message.ErrorSlEntryMsg;
                    this.showErrorMessage = true;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                }
            }).catch((res) => {
                this._globalURL.isLoading = false;
                this.message = this._common_message.CatchBlockMsg;
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            })
    }
    reRenderDT() {
        this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
            dtInstance.destroy();
            this.dtTrigger.next();
        });
    }
    convertToWordsReq(num) {
        this.convertedWordsInEnglish = this._commonService.convertDigitToWordsInEnglish(num);
        this.convertedWordsInHindi = this._commonService.convertDigitToWordsInHindi(num);
        this.convertedWordsInMarathi = this._commonService.convertDigitToWordsInMarathi(num);
    }
}



