import { Injectable } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import { Headers, Response, RequestOptions } from '@angular/http';
import { EmpModel } from '../services/empModel';
import { Http } from '@angular/http'


import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';
//import 'rxjs/add/Observable/throw';
import 'rxjs/add/operator/toPromise';

@Injectable()

export class CMRCService {
    options: RequestOptions;
    private finalURL: string;
    private finalParam1: string | number;
    private finalParam2: string | number;
    private finalParam3: string | number;
    private paramFlag1: boolean;
    private paramFlag2: boolean;
    private paramFlag3: boolean;

    constructor(private _http: Http) {
        this.finalURL = null;
    }

    //Get request with no parameter
    get(url: string, token: string): Promise<any> {
        return this._http.get(url, this.getPutHeaders(token))
            .map((response: Response) => <any>response.json())
            .toPromise()
            .catch();
    }

    private checkParam(para: string | number): boolean {
        if (para.toString() == "" || para.toString() == undefined || para == undefined)
            return false;
        else return true;
    }

    post(url: string, modeldata: any, token: string): Promise<any> {
        debugger;
        return this._http
            .post(url, modeldata, this.getPutHeaders(token))
            .toPromise()
            .catch();
    }

    //To Define the Header information
    private getPutHeaders(_token: string) {
        let headers = new Headers();
        var __token = 'Bearer ' +_token.replace(/['"]+/g, '');     
        headers.append('Content-Type', 'application/json');
        headers.append('Authorization',__token);
        return new RequestOptions({
            headers: headers
        });
    }

    private extractData(res: Response) {
        let body = res.json();
        return body || {};
    }
}



