import { Component, OnInit, ViewChild, AfterViewInit } from '@angular/core';
import { constructor } from 'events';
import { Router } from '@angular/router';
import { AccessToken } from '../../../environments/access_token';
import { GlobalAssets } from '../../../environments/Global';
import { CMRCService } from '../../../services/cmrc.services';
import { CommonMessageComponent } from "../../../environments/CommanMssage";
import { GlobalVariable } from '../../../environments/GlobalVeriables';
import { CmrcDetailsModel } from './cmrcdetails.model.component';
import { NgForm } from '@angular/forms';
import { DataTableDirective } from 'angular-datatables';
import { Subject } from 'rxjs/Subject';
import { Title } from '../../../../../node_modules/@angular/platform-browser';
import { TitleConstants } from '../../../utils/constants/title-constants';

@Component({
    templateUrl: '../cmrc/cmrcdetails.component.html',
    styleUrls: ['../../../components/app.css']

})

export class CmrcDetailsComponent implements OnInit {
    public talukas: any;
    public ditricts: any;
    public areas: any;
    public showWarningMessage: boolean;
    public Cmrcs: any;
    public viewData: any;
    public CmrcDetailas: any;
    public showErrorMessage: boolean;
    public showMessage: boolean;
    public isCmrcDetail: boolean = true;
    public message: any;
    public cmrcdata: any;
    public style_Conf: string = "none";
    public display: string;    
     @ViewChild(DataTableDirective)
     dtElement: DataTableDirective;
     dtInstance: DataTables.Api;
     public dtTrigger: Subject<any> = new Subject();
    constructor(public _cmrcservice: CMRCService,
        public _router: Router,
        public cmrcdetailsmodel: CmrcDetailsModel,
        public _common_message: CommonMessageComponent,
        public _globalURL: GlobalAssets,
        public globalVariable: GlobalVariable,
        public titleService: Title
    ) {
        this.setTitle(TitleConstants.DISTRICT_CMRC_DETAILS);
        this.display = 'none';
        _globalURL.isLoading = true;
        if (sessionStorage.getItem("access_token") == "") {
            _router.navigate(['../login']);
        }
    }
    ngOnInit() {
        this.getCmrcDetails();
        this.getCMRKVP();
        this.getAreaKVP();
        this.getAllDistrict();
    }
    public setTitle(newTitle: string) {
        this.titleService.setTitle(newTitle);
    }
    viewCmrcDetails(data: any) {
        this.viewData = data;
    }

    ngAfterViewInit(): void {
        this.dtTrigger.next();
    }
    getCmrcDetails(): void {
        debugger;
        this.cmrcdata = null;
        this._globalURL.isLoading = true;
        this._cmrcservice.get(this._globalURL.GET_ALL_CMRC_DETAILS, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.cmrcdata = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this._globalURL.isLoading = false;
                }
               this.reRenderDT();
            }).catch((err) => {

                this._globalURL.isLoading = false;
                this.showErrorMessage = true;
                this.message = this._common_message.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                },5000);
            })
    }

    getCMRKVP(): void {
        this._globalURL.isLoading = true;
        this._cmrcservice.get(this._globalURL.GET_KVP_DISTRICT_CMRC_DASHBOARD, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.Cmrcs = res.Data;
                    this.isCmrcDetail = true;
                    this._globalURL.isLoading = false;
                }
                else {
                    this._globalURL.isLoading = false;
                }
            }).catch((err) => {
                this._globalURL.isLoading = false;
                this.showErrorMessage = true;
                this.message = this._common_message.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            })
    }

    forUpdate(data: any) {
        debugger;
        this.cmrcdetailsmodel = data;
    }

    updateCmrcDetail(data: CmrcDetailsModel) {
        debugger;
        this._globalURL.isLoading = true;
        this._cmrcservice.post(this._globalURL.UPDATE_CMRC_DETAILS, data, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == undefined) {
                    if (res.status == 200) {
                        this.getCmrcDetails();
                        this._globalURL.isLoading = false;
                        this.showMessage = true;
                        this.message = this._common_message.UpdateMsg;
                        setInterval(() => {
                            this.showMessage = false;
                        }, 5000);
                    }
                }
                else {
                    this._globalURL.isLoading = false;
                    this.showErrorMessage = true;
                    this.message = this._common_message.WarningMsg;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                }
            }).catch((res) => {
                console.log(res.error);
                this._globalURL.isLoading = false;
                this.showErrorMessage = true;
                this.message = this._common_message.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            });
    }

    getAllDistrict(): void {
        this._globalURL.isLoading = true;
        this._cmrcservice.get(this._globalURL.GET_DISTRICT, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this._router.navigate(['../login']);
                }
                if (res.Status == true) {
                    this.ditricts = res.Data;
                    this._globalURL.isLoading = false;
                    //this.reRenderDT();
                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._common_message.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                }

            }).catch((res) => {
                this._globalURL.isLoading = false;
                this.showErrorMessage = true;
                console.log(res.error);
                this.message = this._common_message.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
               
            })
    }

    getselectTaluka(data: any) {
        debugger;
        var id = data;
        this._globalURL.isLoading = true;
        let finalURl = this._globalURL.GET_DISTRICT_BY_TALUKA_KEY_VALUE_PAIR + "" + data;
        this._cmrcservice.get(finalURl, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this._router.navigate(['../login']);
                }
                if (res.Status == true) {
                    this.talukas = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._common_message.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                }
            }).catch((res) => {
                this._globalURL.isLoading = false;
                this.showErrorMessage = true;
                console.log(res.error);
                this.message = this._common_message.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                
            })
    }


    getAreaKVP(): void {
        this._globalURL.isLoading = true;
        this._cmrcservice.get(this._globalURL.GET_AREA_KVP, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                }
                if (res.Status == true) {
                    this.areas = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._common_message.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                }
            }).catch((err) => {
                this._globalURL.isLoading = false;
                this.message = this._common_message.CatchBlockMsg;
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            });
    }

    reRenderDT() {
        this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
            dtInstance.destroy();
            this.dtTrigger.next();
        });
    }
}


