import { Component, ViewChild } from "@angular/core";
import { DISTRICTService } from "../../../services/district.services";
import { GlobalVariable } from "../../../environments/GlobalVeriables";
import { GlobalAssets } from "../../../environments/Global";
import { AccessToken } from "../../../environments/access_token";
import { LangulageConf } from "../../../environments/language.config";
import { Router } from "@angular/router";
import { DataTableDirective } from "angular-datatables";
import { Subject } from "rxjs";
import { MemberShareMasterModel } from "./memShare.model.components";
import { CommonMessageComponent } from "../../../environments/CommanMssage";
import { CommonService } from "../../../services/common.service";
import { Title } from "../../../../../node_modules/@angular/platform-browser";
import { TitleConstants } from "../../../utils/constants/title-constants";



@Component({
    templateUrl: '../memShare/memShare.component.html',
    styleUrls: ['../../../components/app.css']
})

export class DistrictMemberShareComponent {
    public Cmrcs: any;
    public shgs: any;
    public Vos: any;
    // @ViewChild(DataTableDirective)
    // dtElement: DataTableDirective;
    // dtInstance: DataTables.Api;
    // public dtTrigger: Subject<any> = new Subject();
    public selectedShgId: number;
    public convertedWordsInEnglish: string;
    public convertedWordsInMarathi: string;
    public convertedWordsInHindi: string;
    public selectedVoId: number;
    public memShares: any;
    public message: string;
    public showMessage: boolean;
    public showErrorMessage: boolean;
    public showWarningMessage: boolean;
    public dtTrigger: Subject<any> = new Subject();
    @ViewChild(DataTableDirective)
    dtElement: DataTableDirective;
    dtInstance: DataTables.Api;
    constructor(public _router: Router,
        public lang: LangulageConf,
        public _access_token: AccessToken,
        public _globalURL: GlobalAssets,
        public _globalVariable: GlobalVariable,
        public _districtService: DISTRICTService,
        public _commonMessage: CommonMessageComponent,
        public _commonService: CommonService,
        public memberShareDetails: MemberShareMasterModel,
        public titleService: Title) {
            this.setTitle(TitleConstants.DISTRICT_SHARE_DETAILS);
    }

    ngOnInit() {
        //this.getVoIdAndName();
       // this.getMemShareDetails();
        this.getCMRCDashboardKVP();
    }
    ngAfterViewInit(){
        this.dtTrigger.next();
    }
    public setTitle(newTitle: string) {
        this.titleService.setTitle(newTitle);
    }
    getShgNameAndId(voID: any): void {
        debugger;
        this._globalURL.isLoading = true;
        this._districtService.get((this._globalURL.GET_SHGNAME_ID_VOID + "" + voID), sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.shgs = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this._globalURL.isLoading = false;
                    this.showWarningMessage = true;
                    this.message = this._commonMessage.WarningMsg;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    },5000);
                }
            }).catch((error) => {
                this._globalURL.isLoading = false;
                this.showErrorMessage = true;
                this.message = this._commonMessage.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                },5000);
            })
    }

    getVoByCmrcId(selectCmrc: any): void {
        debugger;
        this._globalURL.isLoading = true;
        this._districtService.get((this._globalURL.GET_VO_BY_CMRC_ID + "?cmrcId=" + selectCmrc), sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.Vos = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this._globalURL.isLoading = false;
                    this.showWarningMessage = true;
                    this.message = this._commonMessage.WarningMsg;
                    setInterval(() =>{
                        this.showWarningMessage = false;
                    });
                }
            }).catch((error)=> {
                this._globalURL.isLoading = false;
                this.showErrorMessage = true;
                this.message = this._commonMessage.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                },5000);
            })
    }

    getMemShareDetails(): void {
        this._globalURL.isLoading = true;
        debugger;
        this.memShares = null;
        this._districtService.get((this._globalURL.GET_SHARE_AMOUNT + "?voId=" + this.selectedVoId + "&shgId=" + this.selectedShgId),
            sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.memShares = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this._globalURL.isLoading = false;
                    this.showErrorMessage = true;
                    this.message = this._commonMessage.WarningMsg;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                }
                this.reRenderDT();
            }).catch((error) => {
                this._globalURL.isLoading = true;
                this.showErrorMessage = true;
                this.message = this._commonMessage.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                },5000);
            })
    }


    getCMRCDashboardKVP(): void {
        debugger;
        this._globalURL.isLoading = true;
        this._districtService.get(this._globalURL.GET_KVP_DISTRICT_CMRC_DASHBOARD, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.Cmrcs = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this._globalURL.isLoading = false;
                    this.showWarningMessage = false;
                    this.message = this._commonMessage.CatchBlockMsg;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    },5000);
                }
            }).catch((error) => {
                this._globalURL.isLoading = false;
                this.showErrorMessage = true;
                this.message = this._commonMessage.CatchBlockMsg;
                setInterval(() => {
                   this.showErrorMessage = false;
                },5000);
            })
    }


    public setUpdate(memberShareDetail: any) {
        this.convertToWords(memberShareDetail.ShareAmount );
        this.memberShareDetails = memberShareDetail;
    }

    public confirmUpdate(data: any) {
        debugger;
        let shareDetails = this._globalURL.UPDATE_DISTRICT_MEMBER_SHARE_DETAILS + "?memId=" + data.Id + "&shareAmount=" + data.ShareAmount + "&paidDate=" + data.UpdatedDate;
        this._districtService.post(shareDetails, null, sessionStorage.getItem("access_token"))
            .then((res) => {
                if ((res.Status) == undefined) {
                    if (res.status === 200) {
                        debugger;
                        this._globalURL.isLoading = false;
                        this.message = this._commonMessage.UpdateMsg;
                        this.showMessage = true;
                        setInterval(() => {
                            this.showMessage = false;
                        }, 5000);
                    }
                    else {
                      this._globalURL.isLoading = false;
                      this.showWarningMessage = true;
                      this.message = this._commonMessage.CatchBlockMsg;
                      setInterval(() => {
                          this.showWarningMessage = false;
                      },5000);
                    }
                }

            }).catch((error) => {
               this._globalURL.isLoading = false;
               this.showErrorMessage = true;
               this.message = this._commonMessage.CatchBlockMsg;
               setInterval(() => {
                   this.showErrorMessage = false;
               },5000);
            })
    }
    reRenderDT() {
        this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
            dtInstance.destroy();
            this.dtTrigger.next();
        });
    }

convertToWords(num) {
        debugger;
        this.convertedWordsInEnglish = this._commonService.convertDigitToWordsInEnglish(num);
        this.convertedWordsInHindi = this._commonService.convertDigitToWordsInHindi(num);
        this.convertedWordsInMarathi = this._commonService.convertDigitToWordsInMarathi(num);
    }
}


