import { Component, OnInit, ViewChild, AfterViewInit } from '@angular/core';
import { constructor } from 'events';
import { Router } from '@angular/router';
import { AccessToken } from '../../../environments/access_token';
import { GlobalAssets } from '../../../environments/Global';
import { CMRCService } from '../../../services/cmrc.services';
import { GlobalVariable } from '../../../environments/GlobalVeriables';
import { CommonMessageComponent } from '../../../environments/CommanMssage';

@Component({
    templateUrl: '../mavimdetail/mavimdetail.component.html',
    styleUrls: ['../../../components/app.css']
})
export class MavimDetailComponent {
    public pageinfo: any;
    public myData: any;
    public publinfodetails: any;
    public showErrorMessage: boolean;
    public message: any;
    public showWarningMessage: boolean;
    constructor(public _cmrcservice: CMRCService,
        public _router: Router,
        public _globalURL: GlobalAssets,
        public globalVariable: GlobalVariable,
        public _common_message: CommonMessageComponent
    ) {
        _globalURL.isLoading = true;
        if (sessionStorage.getItem("access_token") == "") {
            _router.navigate(['../login']);
        }
    }
    ngOnInit() {
        this.getMavimDetails();
        this.globalVariable.PublicInfoId = null;
        this.globalVariable.publicpageinfo = null;
        this.globalVariable.publicpageTitle = null;
        this.globalVariable.publicInformationTypeId = null;
    }
    navigateToPublicInfo(data: any) {
        debugger;
        sessionStorage.setItem("PublicInfoId", data.Id);
        sessionStorage.setItem("Information", data.Information);
        sessionStorage.setItem("publicpageTitle", data.Title);
        sessionStorage.setItem("publicInformationTypeId", data.PublicInformationTypeId);
        this._router.navigate(['/district/maviminformation']);
    }
    getMavimDetails(): void {
        this._globalURL.isLoading = true;
        this._cmrcservice.get(this._globalURL.GET_PUBLICPAGE_INFORMATION +"?roleId="+sessionStorage.getItem("roleId")+"&id=2"+ "&typeId=7",sessionStorage.getItem("access_token"))
            .then((res) => {         
                if (res.Status == true) {
                    this.myData = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._common_message.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                }
            }).catch((err) => {
                this._globalURL.isLoading = false;
                this.message = this._common_message.CatchBlockMsg;
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            })
    }
}


