import { Component, OnInit, ViewChild, AfterViewInit } from '@angular/core';
import { constructor } from 'events';
import { Router } from '@angular/router';
import { CMRCService } from '../../../../services/cmrc.services';
import { GlobalAssets } from '../../../../environments/Global';
import { GlobalVariable } from '../../../../environments/GlobalVeriables';

@Component({
    templateUrl: './bifurcation.component.html',
    styleUrls: ['../../../../components/app.css']
})

export class PublicCMRCBifurCationComponent {
    public showErrorMessage: boolean;
    public _common_message: any;
    public message: any;
    public showWarningMessage: boolean;
    public bifurcationData: any;
    public forTextbox: boolean = false;
    public forLabel: boolean = true;
    public showMessage: boolean;
    constructor(public _cmrcservice: CMRCService,
        public _router: Router,
        public _globalURL: GlobalAssets,
        public globalVariable: GlobalVariable) {
    }

    changebifurgationdetails() {
        this.forLabel = false;
        this.forTextbox = true;
    }

    ngOnInit() {
        this.getCmrcBifurcation();
        this.globalVariable.Serialnumber = null;
        this.globalVariable.CMRCName = null;
        this.globalVariable.schemeName = null;
        this.globalVariable.TypesName = null;
    }

    navigateToPublicInfo(data: any) {
        sessionStorage.setItem("Serialnumber", data.SrNo);
        sessionStorage.setItem("CMRCName", data.CMRCName);
        sessionStorage.setItem("schemeName", data.SchemeName);
        sessionStorage.setItem("TypesName", data.Details)
        this._router.navigate(['/cmrc/newsupdate']);
    }

    getCmrcBifurcation(): void {
        this._globalURL.isLoading = true;
        debugger;
        this._cmrcservice.get(this._globalURL.GET_CMRC_BIFURCATION + "?roleId=" + sessionStorage.getItem("roleId") + "&id=2", sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.bifurcationData = res.Data;
                    for (var item of this.bifurcationData)
                        this._globalURL.isLoading = false;
                    this.showMessage = true;
                    this.message = this._common_message.AddAreaMsg;
                    setInterval(() => {
                        this.showMessage = false;
                    }, 5000);
                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._common_message.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                }
            }).catch((err) => {
                this._globalURL.isLoading = false;
                this.message = this._common_message.CatchBlockMsg;
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            })
    }
}


