import { Component, OnInit } from '@angular/core';
import { CurrencyPipe } from '@angular/common';
import { VOService } from '../../../services/vo.services';
import { AccessToken } from '../../../environments/access_token';
import { GlobalAssets } from '../../../environments/Global';
import { Router } from '@angular/router';
import { CommonService } from '../../../services/common.service';
import { FileUploadService } from '../../../services/FileService/file_upload.service';
import { GlobalVariable } from '../../../environments/GlobalVeriables';
import { LangulageConf } from '../../../environments/language.config';
import { ExportToPDF } from '../../../services/reports/exportpdf';
import { ExportToExcel } from '../../../services/reports/exportexcel';
import { CommonMessageComponent } from '../../../environments/CommanMssage';
import { DatepickerOptions } from 'ng2-datepicker';
import * as frLocale from 'date-fns/locale/en';
import { CustomFormsModule } from 'ng2-validation';
//import { CSGConstants } from '../../../helpers/CSGConstants';
@Component({
    templateUrl: '../loan/loan.component.html',
    styleUrls: ['../../../components/app.css']
})
export class LoanComponent implements OnInit {
    public changePhotoDetail: any;
    public todaydate: any;
    public showWarningMessage: boolean;
    public showErrorMessage: boolean;
    public convertedWordsInEnglish: string;
    public convertedWordsInMarathi: string;
    public convertedWordsInHindi: string;
    public showMessage: boolean;
    public message: string;
    public shgLoanDetails: any;
    public memberLoanDetails: any;
    public SHGNameAndId: any;
    public selectedShgId: any;
    public selectedMemberId: any;
    public nextCode: string;
    public LoanReason: any;
    public membersNameAndId: any;
    public selectedLoanStatus: number;
    public loanId: number = 0;
    public RequestDate: Date = new Date();
    public LoanAmount: number = 0;
    options: DatepickerOptions = {
        minYear: 2000,
        maxYear: (new Date().getFullYear() + 1),
        displayFormat: 'MMM D[,] YYYY',
        barTitleFormat: 'MMMM YYYY',
        dayNamesFormat: 'dd',
        firstCalendarDay: 0,
        locale: frLocale,
        barTitleIfEmpty: 'Click to select a date'
    };

    constructor(public _voservice: VOService,
        private _fileUploadService: FileUploadService,
        public _access_token: AccessToken,
        public _globalURL: GlobalAssets,
        public _commonService: CommonService,
        public exportToPdf: ExportToPDF,
        public exportToExcel: ExportToExcel,
        public router: Router,
        public lang: LangulageConf,
        public globalVariable: GlobalVariable,
        public _common_message: CommonMessageComponent) {
        this.shgLoanDetails = null;
        this.memberLoanDetails = null;
        this.SHGNameAndId = null;
        this.selectedShgId = null;
        this.membersNameAndId = null;
        this.selectedMemberId = null;
        this.LoanReason = null;
        this.selectedLoanStatus = 0;
        if (sessionStorage.getItem("access_token") == "")
            router.navigate(['../login']);
    }

    ngOnInit() {
        this._globalURL.isLoading = true;
        this.loadSHGLoanData()
        this.selectedLoanStatus = this.globalVariable.ACTIVE_SHG;
        this.loanMemberLoanDataByStaus(this.globalVariable.ACTIVE_SHG);
    }

    loanMemberLoanDataByStaus(loanStatus: number) {
        this.selectedLoanStatus = loanStatus;
        this._globalURL.isLoading = true;
        this._voservice.get((this._globalURL.MEMBER_LOAN_DETAILS + "" + loanStatus), sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.memberLoanDetails = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._common_message.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                        this._globalURL.isLoading = false;
                    }, 5000);
                }
            })
            .catch((res) => {
                this.showErrorMessage = true;
                this.message = this._common_message.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                    this._globalURL.isLoading = false;
                }, 5000);
            })
    }

    naviagetToRepayment(memid: any, loanAmt: any) {
        this._globalURL.selectedLoanId = memid;
        this._globalURL.loanAmount = loanAmt;
        this.router.navigate(['/vo/loanrepayment']);
    }

    public totLoanAmt: any;
    public totLoanRepaiddAmt: any;
    public totLoanOutstandingAmt: any;
    public totLoanOutstandingPrincipleAmt: any;
    public totLoanOutstandingInterestAmt: any;
    public totLoanFineAmt: any;
    public totLoanRemainingAmt: any;
    loadSHGLoanData() {
        this.totLoanAmt = 0;
        this.totLoanRepaiddAmt = 0;
        this.totLoanOutstandingAmt = 0;
        this.totLoanOutstandingPrincipleAmt = 0;
        this.totLoanOutstandingInterestAmt = 0;
        this.totLoanFineAmt = 0;
        this.totLoanRemainingAmt = 0
        this._globalURL.isLoading = true;
        this._voservice.get(this._globalURL.SHG_LOAN_DETAILS, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.shgLoanDetails = res.Data;
                    if (this.shgLoanDetails != null && this.shgLoanDetails != undefined) {
                        for (var item in this.shgLoanDetails) {
                            debugger;
                            this.totLoanAmt = this.totLoanAmt + this.shgLoanDetails[item].LoanAmount;
                            this.totLoanRepaiddAmt = this.totLoanRepaiddAmt + this.shgLoanDetails[item].SHGPaidAmount;
                            this.totLoanOutstandingAmt = this.totLoanOutstandingAmt + this.shgLoanDetails[item].SHGOutstandingPrincipalAmount + this.shgLoanDetails[item].SHGOutstandingInterest;
                            this.totLoanOutstandingPrincipleAmt = this.totLoanOutstandingPrincipleAmt + this.shgLoanDetails[item].SHGOutstandingPrincipalAmount;
                            this.totLoanOutstandingInterestAmt = this.totLoanOutstandingInterestAmt + this.shgLoanDetails[item].SHGOutstandingInterest;
                            this.totLoanFineAmt = this.totLoanFineAmt + this.shgLoanDetails[item].SHGFineAmt;
                        }
                        this._globalURL.isLoading = false;
                    }
                    else {
                        this.showWarningMessage = true;
                        this.message = this._common_message.WarningMsg;
                        setInterval(() => {
                            this.showWarningMessage = false;
                            this._globalURL.isLoading = false;
                        }, 5000);
                    }
                }
            }).catch((res) => {
                this.showErrorMessage = true;
                this.message = this._common_message.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                    this._globalURL.isLoading = false;
                }, 5000);
            })
    }

    loanSHGNameAndId() {
        this._globalURL.isLoading = true;
        this._voservice.get(this._globalURL.GET_SHG_ID_NAME, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this.router.navigate(['../login']);
                }
                if (res.Status == true) {
                    this.SHGNameAndId = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._common_message.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                }
            })
            .catch((res) => {
                this.showErrorMessage = true;
                this.message = this._common_message.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this._globalURL.isLoading = false;
            })
    }

    loadMembersForSHGId() {
        this._globalURL.isLoading = true;
        this._voservice.get((this._globalURL.GET_MEM_ID_NAME + "" + this.selectedShgId), sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this.router.navigate(['../login']);
                }
                if (res.Status == true) {
                    this.memLoanEligibleMessage = "Check Members Eligibility!";
                    this.isMembEligibleForLoan = false;
                    this.isDefaultLoanState = true;
                    this.membersNameAndId = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._common_message.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                }
            })
            .catch((res) => {
                this.showErrorMessage = true;
                this.message = this._common_message.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this._globalURL.isLoading = false;
            })
    }
    public memLoanEligibleMessage: string = "Check Members Eligibility!";
    public isMembEligibleForLoan: boolean = false;
    public isDefaultLoanState: boolean = true;
    public isMemberRemainingLoanAmountCheck: boolean = false;
    public LoanAmountIfMemberRequestRemainingAmount: number = 0;
    public checkMembersEligibility() {
        if (this.selectedMemberId != 0) {
            this._globalURL.isLoading = true;
            this._voservice.get(this._globalURL.CHECK_LOAN_ELIGIBILITY + this.selectedMemberId, sessionStorage.getItem("access_token"))
                .then((res) => {
                    debugger;
                    this.isDefaultLoanState = false;
                    this._globalURL.isLoading = false;
                    if (res.Data.Key !== 0 && res.Status === true) {
                        this.memLoanEligibleMessage = res.Data.Value;
                        this.isMemberRemainingLoanAmountCheck = false;
                        if (res.Data.Key > 1) {
                            this.LoanAmount = res.Data.Key;
                            this.LoanAmountIfMemberRequestRemainingAmount = res.Data.Key;
                            this.isMemberRemainingLoanAmountCheck = true;
                        }
                        this.isMembEligibleForLoan = res.Status;
                    }
                    else {
                        this.memLoanEligibleMessage = res.Data.Value;
                        this.isMembEligibleForLoan = res.Status;
                    }
                })
                .catch((res) => {
                    this._globalURL.isLoading = false;
                    console.log(res);
                })
        }
    }

    save(data: any, frDt: Date, metadata: any) {
        debugger;
        this._globalURL.isLoading = true;
        data.SHGId = this.selectedShgId;
        data.MemberId = this.selectedMemberId;
        data.LoanAmount = this.LoanAmount;
        this._voservice.post(this._globalURL.CREATE_LOAN_APPLICATION, data, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._common_message.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this.router.navigate(['../login']);
                }
                if (res.Status == undefined) {
                    if (res.status == 200) {
                        this.selectedLoanStatus == this.globalVariable.NEW_LOAN;
                        var data = JSON.parse(res._body);
                        this._globalURL.isLoading = false;
                        this.loadSHGLoanData();
                        this.loanMemberLoanDataByStaus(this.globalVariable.ACTIVE_SHG);
                        var formData = new FormData();
                        formData.append("RefId", data.Data);
                        formData.append("file", metadata);
                        this._fileUploadService.post((this._globalURL.ADD_PHOTO + "?folderName=Loan" + "&type=" + this._globalURL.LOAN_APPLICATION_PHOTO + "&RefId=" + data.Data + "&PhotoId=0"),
                            formData, sessionStorage.getItem("access_token")).then((res) => {
                                if (res.status === 401) {
                                    sessionStorage.clear();
                                    this.showErrorMessage = true;
                                    this.message = this._common_message.error401message;
                                    setInterval(() => {
                                        this.showErrorMessage = false;
                                    }, 5000);
                                    this._globalURL.isLoading = false;
                                    this.router.navigate(['../login']);
                                }
                                if (res.Status == undefined) {
                                    if (res.status == 200) {
                                        this.message = this._common_message.CreateLoanMsg;
                                        this.showMessage = true;
                                        setInterval(() => {
                                            this.showMessage = false;
                                        }, 6000);
                                    }
                                }
                                else {
                                    this.showErrorMessage = true;
                                    this.message = this._common_message.CatchBlockMsg;
                                    setInterval(() => {
                                        this.showErrorMessage = false;
                                    }, 5000);
                                    this._globalURL.isLoading = false;
                                }
                            }).catch((res) => {
                                this.showErrorMessage = true;
                                this.message = this._common_message.CatchBlockMsg;
                                setInterval(() => {
                                    this.showErrorMessage = false;
                                }, 5000);
                                this._globalURL.isLoading = false;
                            })
                    }
                }
            }).catch((res) => {
                this.showErrorMessage = true;
                this.message = this._common_message.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this._globalURL.isLoading = false;
            })
    }

    convertToWords(num) {
        this.convertedWordsInEnglish = this._commonService.convertDigitToWordsInEnglish(num);
        this.convertedWordsInHindi = this._commonService.convertDigitToWordsInHindi(num);
        this.convertedWordsInMarathi = this._commonService.convertDigitToWordsInMarathi(num);
    }

    setLoanId(loanId: any) {
        this._globalURL.selectedLoanId = loanId;
    }

    AddRemainingToOutstanding(loanId: number) {
        //this._voservice.post(this._globalURL.)
    }

    SHGLoanBifurcationExportToPdf() {
        this._globalURL.isLoading = true;
        this.exportToPdf.SHGLoanBifurcationExportToPdf(this.shgLoanDetails);
        this._globalURL.isLoading = false;
    }

    loanDetailExportToExcel() {
        this.exportToExcel.loanDetailExportToExcel(this.shgLoanDetails);
    }

    public chnagePhoto(metadata: any, refId: string, phtoId?: number) {
        debugger;
        var formData = new FormData();
        formData.append("RefId", refId);
        formData.append("file", metadata);
        this._fileUploadService.post((this._globalURL.ADD_PHOTO + "?folderName=Loan" + "&type=" + this._globalURL.LOAN_APPLICATION_PHOTO + "&RefId=" + refId + "&PhotoId=" + (phtoId == 0 || phtoId == null ? 0 : phtoId)),
            formData, sessionStorage.getItem("access_token")).then((res) => {
                if (res.Status == undefined) {
                    if (res.status == 200) {
                        this.loanMemberLoanDataByStaus(this.globalVariable.NEW_LOAN);
                        this.showMessage = true;
                        this.message = this._common_message.PhotoChangeMsg;
                        setInterval(() => {
                            this.showMessage = false;
                        }, 5000);
                    }
                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._common_message.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                }
            }).catch((res) => {
                this.showErrorMessage = true;
                console.log(res.error);
                this.message = this._common_message.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this._globalURL.isLoading = false;
            })
    }
    forchange(data: any) {
        this.changePhotoDetail = data;
    }

}


