import { Component, OnInit, ViewChild, AfterViewInit } from '@angular/core';
import { constructor } from 'events';
import { Router } from '@angular/router';
import { CMRCService } from '../../../../services/cmrc.services';
import { GlobalAssets } from '../../../../environments/Global';
import { GlobalVariable } from '../../../../environments/GlobalVeriables';
import { CommonMessageComponent } from '../../../../environments/CommanMssage';
import { FileUploadService } from '../../../../services/FileService/file_upload.service';
import { NgForm } from '@angular/forms';
import { PublicPageConstants } from '../../../../utils/constants/public-page-constants';
@Component({
    templateUrl: './cmrc-photos.component.html',
    styleUrls: ['../../../../components/app.css']
})

export class CMRCPublicPhotos {
    public ShowErrormsg: boolean;
    public showSucessMessage: boolean;
    public showMessage: boolean;
    public pageinfo: any;
    public myData: any;
    public publinfodetails: any;
    public showErrorMessage: boolean;
    public message: any;
    public showWarningMessage: boolean;
    public CMRCPRESIDENT: boolean = false;
    public Newsphoto: boolean = false;
    constructor(public _cmrcservice: CMRCService,
        public _router: Router,
        public _globalURL: GlobalAssets,
        public globalVariable: GlobalVariable,
        private _fileUploadService: FileUploadService,
        public _common_message: CommonMessageComponent) {
        _globalURL.isLoading = true;
        if (sessionStorage.getItem("access_token") == "") {
            _router.navigate(['../login']);
        }
    }

    ngOnInit() {
        alert(PublicPageConstants.MAVIMIdentity);
        this.getPublicInformation();
        this.globalVariable.PublicInfoId = null;
        this.globalVariable.publicpageinfo = null;
        this.globalVariable.publicpageTitle = null;
        this.globalVariable.publicpageCaption = null;
        this.globalVariable.publicInformationTypeId = null;
    }
    navigateToPublicInfo(data: any) {
        sessionStorage.setItem("PublicInfoId", data.Id);
        sessionStorage.setItem("publicpageinfo", data.Information);
        sessionStorage.setItem("publicpageTitle", data.Title);
        sessionStorage.setItem("publicpageCaption", data.CaptionName);
        sessionStorage.setItem("publicInformationTypeId", data.InformationTypeId);
        this._router.navigate(['/cmrc/eventsupdate']);
    }

    getPublicInformation(): void {
        debugger
        this._globalURL.isLoading = true;
        this._cmrcservice.get(this._globalURL.GET_PUBLICPAGE_INFORMATION + "?roleId=" + sessionStorage.getItem("roleId") + "&typeId=" + this.globalVariable.cmrcPhotosId, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.myData = res.Data;
                    for (var item of this.myData)
                        this._globalURL.isLoading = false;
                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._common_message.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                }
            }).catch((err) => {
                this._globalURL.isLoading = false;
                this.message = this._common_message.CatchBlockMsg;
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            })
    }

    public chnagePhoto(metadata: any, refId: string, phtoId?: number) {
        debugger;
        var formData = new FormData();
        formData.append("RefId", refId);
        formData.append("file", metadata);
        this._fileUploadService.post((this._globalURL.ADD_PHOTO + "?folderName=Loan" + "&type=" + this._globalURL.LOAN_APPLICATION_PHOTO + "&RefId=" + refId + "&PhotoId=" + (phtoId == 0 || phtoId == null ? 0 : phtoId)),
            formData, sessionStorage.getItem("access_token")).then((res) => {
                if (res.Status == undefined) {
                    if (res.status == 200) {
                        // this.loanMemberLoanDataByStaus(this.globalVariable.NEW_LOAN);
                        this.showMessage = true;
                        this.message = this._common_message.MemberCreatedMsg;
                        setInterval(() => {
                            this.showMessage = false;
                        }, 5000);
                    }
                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._common_message.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                }
            }).catch((res) => {
                this.showErrorMessage = true;
                console.log(res.error);
                this.message = this._common_message.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this._globalURL.isLoading = false;
            })
    }


    public photoForm: NgForm;
    SavePublicInfo(Publicinfo: any, metadata: any) {
        // Publicinfo.Id = sessionStorage.getItem("PublicInfoId");
        // Publicinfo.InformationTypeId = this.globalVariable.cmrcPhotosId;
        this._globalURL.isLoading = true;
        debugger;
        this._cmrcservice.post(this._globalURL.ADD_PUBLIC_INFORMATION, Publicinfo, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status == 200) {
                    var data = JSON.parse(res._body);
                    var formData = new FormData();
                    // formData.append("folderName", "Member");
                    formData.append("RefId", data.Data);
                    formData.append("file", metadata);
                    var publicPhotoViewModel =
                        {
                            "folderName": "Member",
                            "RefId": data.Data,
                            "metadata": formData,
                            "PhotoId": 0
                        };
                    this._fileUploadService.post(this._globalURL.ADD_CMRC_PHOTO + "?folderName=Member" + "&type=" + this._globalURL.CMRC_PHOTO + "&RefId=" + data.Data + "&PhotoId=0", formData, sessionStorage.getItem("access_token"))
                        .then((res) => {
                            if (res.Status == undefined) {
                                if (res.status == 200) {
                                    alert("file upload");
                                    this.photoForm.control.reset();
                                    this.showMessage = true;
                                    this.message = this._common_message.MemberCreatedMsg;
                                    setInterval(() => {
                                        this.showMessage = false;
                                    }, 5000);
                                }
                            }
                            else {
                                this.showWarningMessage = true;
                                this.message = this._common_message.WarningMsg;
                                setInterval(() => {
                                    this.showWarningMessage = false;
                                }, 5000);
                                this._globalURL.isLoading = false;
                            }
                        }).catch((res) => {
                            this.showErrorMessage = true;
                            console.log(res.error);
                            this.message = this._common_message.CatchBlockMsg;
                            setInterval(() => {
                                this.showErrorMessage = false;
                            }, 5000);
                            this._globalURL.isLoading = false;
                        })
                    this._globalURL.isLoading = false;
                    // this.message = " News Information Updated Successfully";
                    this.showSucessMessage = true;
                    setInterval((res) => {
                        this.showSucessMessage = false;
                    }, 5000),

                        this._globalURL.isLoading = false;
                }

                else {
                    this._globalURL.isLoading = false;
                    this.showSucessMessage = true;
                    this.message = this._common_message.ErrorMessage;
                    setInterval(() => {
                        this.showSucessMessage = false;
                    }, 5000);
                }
                // this.navigateToNews();
                // this._router.navigate(['/cmrc/news']);
            }).catch((res) => {
                this._globalURL.isLoading = false;
                this.ShowErrormsg = true;
                this.message = this._common_message.CatchBolockMsg;
                setInterval(() => {
                    this.ShowErrormsg = false;
                }, 5000);
            });
    }


}


