import { Component, OnInit } from '@angular/core';
import { VOService } from '../../services/vo.services';
 import { EmpModel } from '../../services/empModel';
import { GlobalAssets } from '../../environments/Global';
import { Router } from '@angular/router';
import { LangulageConf } from '../../environments/language.config';
@Component({
    templateUrl: '../admin/index.html'
})

export class ADMINBaseComponent implements OnInit { 
    public today: Date;
    constructor(public _voService: VOService
        , public _globalURL: GlobalAssets, 
        public lang: LangulageConf,
        public router: Router) {  
            this.today = new Date();
    }
    ngOnInit() {

    } 
    setLang(lang_) {
        if (lang_ === "en") {
            this.lang.en = true;
            this.lang.mr = false;
        }
        if (lang_ === "mr") {
            this.lang.mr = true;
            this.lang.en = false;
        }      
    }
}


