import { Component } from '@angular/core';
import { CMRCService } from '../../../services/cmrc.services';
import { Router } from '@angular/router';
import { CmrcAdminModel } from '../cmrc/cmrc.model';
import { GlobalAssets } from '../../../environments/Global';
import { GlobalVariable } from '../../../environments/GlobalVeriables';
@Component({
    templateUrl: '../district/district.component.html',
})

export class DistrictComponent {
    public status: number;
    public ActivateBtn: boolean;
    public DeActivateBtn: boolean;
    public viewDistrictData: any;
    public districtUpdateModel: any;
    public districtdata: any;
    public message: string;
    public ShowErrormsg: boolean;
    public showErrorMessage: boolean;
    public showMessage: boolean;
    constructor(public _cmrcservice: CMRCService,
        public _router: Router,
        public cmrcadminmodel: CmrcAdminModel,
        public _globalURL: GlobalAssets,
        public globalVariable: GlobalVariable,

    ) {
        this.ActivateBtn = true;
        this.DeActivateBtn = false;
        if (sessionStorage.getItem("access_token") == "") {
            _router.navigate(['../login']);
        }
    }


    ngOnInit(): void {
        this.getDistrictDetails();

    }

    changeDistrictStatus(districtId: number, status: any) {
        debugger;

        var updateDistrictStatus = this._globalURL.UPDATE_DISTRICT_STATUS + districtId + "&status=" + status;
        this._cmrcservice.post(updateDistrictStatus, null, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this._router.navigate(['../login']);
                }
                if (res.Status == undefined) {
                    var data = JSON.parse(res._body);
                    if (data.Status == true) {
                        this._globalURL.isLoading = false;
                        this.showMessage = true;
                        setInterval(() => {
                            this.showMessage = false;
                        }, 5000);
                        this.getDistrictDetails();
                    }
                    else {
                        this._globalURL.isLoading = false;
                        this.showErrorMessage = true;
                        setInterval(() => {
                            this.showErrorMessage = false;
                        }, 5000);
                    }
                }
                else {
                    if (res.Status == true) {
                        this._globalURL.isLoading = false;
                        this.showMessage = true;
                        setInterval(() => {
                            this.showMessage = false;
                        }, 5000);
                        this.getDistrictDetails();
                    }
                    else {

                        setInterval(() => {
                        }, 5000);
                        this._globalURL.isLoading = false;
                    }
                }
            })
            .catch((err) => {
                this._globalURL.isLoading = false;
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            })
    }

    createDISTRICT(data: any): void {
        debugger;
        this._globalURL.isLoading = true;
        this._cmrcservice.post(this._globalURL.ADD_DISTRICT, data, sessionStorage.getItem("access_token"))
            .then((res) => {
                debugger;
                if (res.Status == undefined) {
                    if (res.status === 200) {
                        var data = JSON.parse(res._body);
                        this._globalURL.isLoading = false;
                        this.showMessage = true;
                        setInterval(() => {
                            this.showMessage = false;
                        }, 5000);
                    }
                }
            }).catch((err) => {
                this.ShowErrormsg = true;
                this.message = "Record is not created";
                setInterval(() => {
                }, 5000);
            })

    }

    getDistrictDetails() {
        debugger;
        this._cmrcservice.get(this._globalURL.GET_ALL_DISTRICT, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.districtdata = res.Data;
                }
                this._globalURL.isLoading = false;
            })
            .catch((err) => {
                this.ShowErrormsg = true;
                this.message = "Record is not created";
                setInterval(() => {
                    this.ShowErrormsg = false;
                    this._globalURL.isLoading = false;
                }, 5000);
            })
    }



    viewDistrictDetail(data: any): void {
        debugger;
        this.viewDistrictData = data;
    }



    setUpdateData(data: any) {
        debugger
        this.districtUpdateModel = data;
    }

    updateDistrict() {
        debugger;
        this._cmrcservice.post(this._globalURL.UPDATE_DISTRICT, this.districtUpdateModel, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this._router.navigate(['../login']);
                }
                if (res.Status == undefined) {
                    var data = JSON.parse(res._body);
                    if (data.Status == true) {
                        this._globalURL.isLoading = false;
                        this.showMessage = true;
                        setInterval(() => {
                            this.showMessage = false;
                        }, 5000);
                        this.getDistrictDetails();
                    }
                    else {
                        this._globalURL.isLoading = false;
                        this.showErrorMessage = true;
                        setInterval(() => {
                            this.showErrorMessage = false;
                        }, 5000);
                    }
                }
                else {
                    if (res.Status == true) {
                        this._globalURL.isLoading = false;
                        this.showMessage = true;
                        setInterval(() => {
                            this.showMessage = false;
                        }, 5000);
                        this.getDistrictDetails();
                    }
                    else {

                        setInterval(() => {
                        }, 5000);
                        this._globalURL.isLoading = false;
                    }
                }
            })
            .catch((err) => {
                this._globalURL.isLoading = false;
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            })
    }


}


