import { Component, ViewChild } from "../../../../../node_modules/@angular/core";
import { DISTRICTService } from "../../../services/district.services";
import { Router } from "../../../../../node_modules/@angular/router";
import { LangulageConf } from "../../../environments/language.config";
import { AccessToken } from "../../../environments/access_token";
import { GlobalAssets } from "../../../environments/Global";
import { GlobalVariable } from "../../../environments/GlobalVeriables";
import { CommonService } from "../../../services/common.service";
import { CommonMessageComponent } from "../../../environments/CommanMssage";
import { DataTableDirective } from "../../../../../node_modules/angular-datatables";
import { DatepickerOptions } from "ng2-datepicker";
import * as frLocale from 'date-fns/locale/en';
import { voSLDetailsModel } from "./voSLDetails.model.component";
import { Subject } from "../../../../../node_modules/rxjs";
import { Title } from "../../../../../node_modules/@angular/platform-browser";
import { TitleConstants } from "../../../utils/constants/title-constants";
@Component({
    templateUrl : '../voSL/voSLDetails.component.html',
    styleUrls: ['../../../components/app.css']
    
})

export class voSLDetailsComponent{
    public Cmrcs: any;
    public convertedWordsInEnglish: string;
    public convertedWordsInMarathi: string;
    public convertedWordsInHindi: string;
    public selectedVOId: any;
    public cmrcSLDetails: any;
    public Vos: any;
    public lastFinanceYearOrFromDt: string;
    public todaysOrToDt: string;
    public showSuccMessage: boolean = false;
    public showErrorMessage: boolean = false;
    public message: any;
    public showWarningMessage: boolean;
    public showMessage: boolean;
    public slEntriesDetails: any;
    @ViewChild(DataTableDirective)
     dtElement: DataTableDirective;
     dtInstance: DataTables.Api;
     public dtTrigger: Subject<any> = new Subject();
    public ToDt: Date = new Date();
    public FromDt: Date = new Date();
    options: DatepickerOptions = {
        minYear: 2000,
        maxYear: (new Date().getFullYear() + 1),
        displayFormat: 'MMM D[,] YYYY',
        barTitleFormat: 'MMMM YYYY',
        dayNamesFormat: 'dd',
        firstCalendarDay: 0, // 0 - Sunday, 1 - Monday
        locale: frLocale,
        barTitleIfEmpty: 'Click to select a date'
    };
 
    constructor(public districtService: DISTRICTService,
        public _router: Router,
        public lang: LangulageConf,
        public _access_token: AccessToken,
        public _globalURL: GlobalAssets,
        public _globalVariable: GlobalVariable,
        public _commonService: CommonService,
        public _commonMessage: CommonMessageComponent,
        public _voSLDetailsModel: voSLDetailsModel,
        public titleService: Title){
            this.setTitle(TitleConstants.DISTRICT_VO_SL_DETAILS);
    }

    ngOnInit(){
        this.getCMRCsKVP();
    }

    ngAfterViewInit(): void {
        this.dtTrigger.next();
    }

    public setTitle(newTitle: string) {
        this.titleService.setTitle(newTitle);
    }
    getCMRCsKVP(): void {
        debugger;
        this._globalURL.isLoading = true;
        this.districtService.get(this._globalURL.GET_KVP_DISTRICT_CMRC_DASHBOARD, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.Cmrcs = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this._globalURL.isLoading = false;
                    this.showWarningMessage = true;
                    this.message = this._commonMessage.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    },5000);
                }
            }).catch((error) => {
              this._globalURL.isLoading = false;
              this.showErrorMessage = true;
              this.message = this._commonMessage.CatchBlockMsg;
              setInterval(() => {
                this.showWarningMessage = false;
            },5000);
            })
    }

    getVoByCmrcId(selectCmrc:any): void{
        debugger;
        this._globalURL.isLoading = true;
        this.districtService.get((this._globalURL.GET_VO_BY_CMRC_ID + "?cmrcId=" + selectCmrc), sessionStorage.getItem("access_token"))
        .then((res) => {
            if(res.Status == true){
                this.Vos = res.Data;
                this._globalURL.isLoading = false;
            }
            else{
                this._globalURL.isLoading = false;
                this.showWarningMessage = true;
                this.message = this._commonMessage.WarningMsg;
                setInterval(() => {
                    this.showWarningMessage = false;
                },5000);
            }
        }).catch((error) => {
            this._globalURL.isLoading = false;
            this.showErrorMessage = true;
            this.message = this._commonMessage.CatchBlockMsg;
            setInterval(() => {
              this.showWarningMessage = false;
          },5000);
          })
    }
    
    getSLDetails(voId: any): void {
        debugger;
        this.districtService.get((this._globalURL.GET_CMRCS_SL_DETAILS + "?roleId=3" + "&id=" + voId), sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.cmrcSLDetails = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this._globalURL.isLoading = false;
                    this.showWarningMessage = true;
                    this.message = this._commonMessage.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    },5000);
                }
            }).catch((error) => {
                this._globalURL.isLoading = false;
                this.showErrorMessage = true;
                this.message = this._commonMessage.CatchBlockMsg;
                setInterval(() => {
                  this.showWarningMessage = false;
              },5000);
              })
    }

    saveRecord(selectedId : any): void{
        this.selectedVOId = selectedId;
    }


    loadSLDetails(frDt: any, toDt: any) {
        debugger;
        this.lastFinanceYearOrFromDt = this._commonService.convertToDate(frDt);
        this.todaysOrToDt = this._commonService.convertToDate(toDt);
        var urls = this._globalURL.GET_SL_ENTRIES_DETAILS + this._globalVariable.VORoleId + "&loginId=" + this.selectedVOId + "&fromdt=" + this.lastFinanceYearOrFromDt + "&todt=" + this.todaysOrToDt;
        this.districtService.get(urls, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._commonMessage.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this._router.navigate(['../login']);
                }
                if (res.Status == undefined)
                    if (res.status == 200) {
                        if ((JSON.parse(res._body).Data.Status == 200))
                            this.slEntriesDetails = JSON.parse(res._body).Data;
                        else {
                            this.showWarningMessage = true;
                            this.message = this._commonMessage.WarningMsg;
                            setInterval(() => {
                                this.showWarningMessage = false;
                            }, 5000);
                            this._globalURL.isLoading = false;
                        }
                    }
                    else {
                        this.showWarningMessage = true;
                        this.message = this._commonMessage.WarningMsg;
                        setInterval(() => {
                            this.showWarningMessage = false;
                        }, 5000);
                        this._globalURL.isLoading = false;
                    }

                else {
                    if (res.Status == true) {
                        this.slEntriesDetails = res.Data;
                        this._globalURL.isLoading = false;
                    }
                    else {
                        this.showWarningMessage = true;
                        this.message = this._commonMessage.WarningMsg;
                        setInterval(() => {
                            this.showWarningMessage = false;
                        }, 5000);
                        this._globalURL.isLoading = false;
                    }
                }
                this.reRenderDT();
            }).catch((res) => {
                this.showErrorMessage = true;
                console.log(res.error);
                this.message = this._commonMessage.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this._globalURL.isLoading = false;
            })
    }

    getSLEntriesDetails(isAllEntries: boolean) {
        debugger;
        this.districtService.get(this._globalURL.GET_SL_ENTRIES_DETAILS + this._globalVariable.VORoleId +"&loginId=" + this.selectedVOId + "&isAllEntries=" + isAllEntries, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status === 401) {
                    sessionStorage.clear();
                    this.showErrorMessage = true;
                    this.message = this._commonMessage.error401message;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                    this._router.navigate(['../login']);
                }
                if (res.Status == true) {
                    this.slEntriesDetails = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this._globalURL.isLoading = false;
                    this.showWarningMessage = true;
                    this.message = this._commonMessage.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                   
                }
                this.reRenderDT();
            }).catch((res) => {
                this._globalURL.isLoading = false;
                this.message = this._commonMessage.CatchBlockMsg;
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            })
    }

    setUpdatedValues(voSLDetail : any): void{
        debugger;
        this.convertToWords(voSLDetail.Amount);
        this._voSLDetailsModel = voSLDetail;
    }
    public confirmUpdate(data: any) {
        debugger;
        let shareDetails = this._globalURL.UPDATE_SL_DETAILS + "?roleId=" + this._globalVariable.VORoleId + "&id=" +  this.selectedVOId + "&transactionTypeId=23" + "&amount=" + data.Amount + "&t_date=" + data.TransactionDate;
        this.districtService.post(shareDetails, null, sessionStorage.getItem("access_token"))
            .then((res) => {
                if ((res.Status) == undefined) {
                    if (res.status === 200) {
                        debugger;
                        this._globalURL.isLoading = false;
                        this.showMessage = true;
                        this.message = this._commonMessage.UpdateMsg;                
                        setInterval(() => {
                            this.showMessage = false;
                        }, 5000);
                    }
                }
                else {
                    this._globalURL.isLoading = false;
                    this.showWarningMessage = true;
                    this.message = this._commonMessage.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                   
                }

            }).catch((error) => {
                this._globalURL.isLoading = false;
                this.message = this._commonMessage.CatchBlockMsg;
                this.showErrorMessage = true;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            })
    }

    reRenderDT() {
        this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
            dtInstance.destroy();
            this.dtTrigger.next();
        });
    }

    convertToWords(num) {
        debugger;
        this.convertedWordsInEnglish = this._commonService.convertDigitToWordsInEnglish(num);
        this.convertedWordsInHindi = this._commonService.convertDigitToWordsInHindi(num);
        this.convertedWordsInMarathi = this._commonService.convertDigitToWordsInMarathi(num);
    }

}




