import { Router } from "@angular/router";
import { LangulageConf } from "../../../environments/language.config";
import { AccessToken } from "../../../environments/access_token";
import { GlobalAssets } from "../../../environments/Global";
import { GlobalVariable } from "../../../environments/GlobalVeriables";
import { CommonService } from "../../../services/common.service";
import { DISTRICTService } from "../../../services/district.services";
import { Component, ViewChild } from "@angular/core";
import { FDMasterModel } from "./fd.model.component";
import { DatepickerOptions } from 'ng2-datepicker';
import * as frLocale from 'date-fns/locale/en';
import { CommonMessageComponent } from "../../../environments/CommanMssage";
import { NgForm } from "@angular/forms";
import { Subject } from "../../../../../node_modules/rxjs";
import { DataTableDirective } from "../../../../../node_modules/angular-datatables";
import { Title } from "../../../../../node_modules/@angular/platform-browser";
import { TitleConstants } from "../../../utils/constants/title-constants";


@Component({
    templateUrl: '../fd/fd.component.html',
    styleUrls: ['../../../components/app.css']
})

export class DistrictFDComponent {
    public Vos: any;
    public Cmrcs: any;
    public FDId: number;
    public ShowReturnFD: boolean;
    public showReturnableFD: boolean;
    public fdModel: any;
    public message: string;
    public showMessage: boolean;
    public showErrorMessage: boolean;
    public showWarningMessage: boolean;
    public CreatedDate: Date = new Date();
    public dtTrigger: Subject<any> = new Subject();
    @ViewChild(DataTableDirective)
    dtElement: DataTableDirective;
    dtInstance: DataTables.Api;
    options: DatepickerOptions = {
        minYear: 2000,
        maxYear: (new Date().getFullYear() + 1),
        displayFormat: 'MMM D[,] YYYY',
        barTitleFormat: 'MMMM YYYY',
        dayNamesFormat: 'dd',
        firstCalendarDay: 0, // 0 - Sunday, 1 - Monday
        locale: frLocale,
        //minDate: new Date(Date.now()), // Minimal selectable date
        //minDate: new Date("2016-03-03"),
        //maxDate: new Date(Date.now()),  // Maximal selectable date
        barTitleIfEmpty: 'Click to select a date'
    };
    constructor(public districtService: DISTRICTService,
        public _router: Router,
        public lang: LangulageConf,
        public _access_token: AccessToken,
        public _globalURL: GlobalAssets,
        public _globalVariable: GlobalVariable,
        public _commonService: CommonService,
        public _commonMessage: CommonMessageComponent,
        public fdData: FDMasterModel,
        public titleService: Title
    ) {
        this.setTitle(TitleConstants.DISTRICT_FD_DETAILS);
        if (sessionStorage.getItem("access_token") == "")
            _router.navigate(['../login']);
    }
    ngOnInit() {
        this.getCMRCsKVP();
    }
    ngAfterViewInit(){
        this.dtTrigger.next();
    }
    public setTitle(newTitle: string) {
        this.titleService.setTitle(newTitle);
    }
    public selectedCMRCID: number = 0;
    public selectedStatus: number = 0;
    getFD(cmrcId: number, status: number): void {
        debugger;
        this.selectedCMRCID = cmrcId;
        this.selectedStatus = status;
        this._globalURL.isLoading = true;
        this.FDId = null;
        this.FDId = status;
        this.fdModel = [];
        this.districtService.get((this._globalURL.GET_FD_FOR_DISTRICT + "?cmrcId=" + cmrcId + "&status=" + status), sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.fdModel = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this._globalURL.isLoading = false;
                    this.showErrorMessage = true;
                    this.message = this._commonMessage.WarningMsg;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                   
                }
                this.reRenderDT();
            }).catch((error) => {
                this._globalURL.isLoading = false;
                this.showErrorMessage = true;
                this.message = this._commonMessage.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                },5000);
            })
    }


    getCMRCsKVP(): void {
        debugger;
        this._globalURL.isLoading = true;
        this.districtService.get(this._globalURL.GET_KVP_DISTRICT_CMRC_DASHBOARD, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.Cmrcs = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this._globalURL.isLoading = false;
                    this.showWarningMessage = true;
                    this.message = this._commonMessage.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    },5000);
                }
            }).catch((error) => {
                this._globalURL.isLoading = false;
                this.showErrorMessage = true;
                this.message = this._commonMessage.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            })
    }

    getVoByCmrcId(selectCmrc: any): void {
        debugger;
        this._globalURL.isLoading = true;
        this.districtService.get((this._globalURL.GET_VO_BY_CMRC_ID + "?cmrcId=" + selectCmrc), sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.Vos = res.Data;
                    this._globalURL.isLoading = false;
                }
                else {
                    this._globalURL.isLoading = false;
                    this.showWarningMessage = true;
                    this.message = this._commonMessage.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    },5000);
                }
            }).catch((error) => {
                this._globalURL.isLoading = false;
                this.showErrorMessage = true;
                this.message = this._commonMessage.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
            })
    }



    public setUpdate(fdDetail: any) {
        debugger;
        this.fdData = fdDetail;
        if (this.FDId == 1) {
            this.ShowReturnFD = false;
            this.showReturnableFD = true;

        }
        if (this.FDId == 2) {
            this.ShowReturnFD = true;
            this.showReturnableFD = false;
        }
    }

    public confirmUpdate(Data: NgForm) {
        debugger;
        Data.value.Id = this.fdData.Id;
        var updata = {

        }
        this.districtService.post(this._globalURL.UPDATE_DISTRICT_FD_DETAILS, Data.value, sessionStorage.getItem("access_token"))
            .then((res) => {
                if ((res.Status) == undefined) {
                    if (res.status === 200) {
                        debugger;
                        Data.control.reset();
                        this._globalURL.isLoading = false;
                        this.getFD(this.selectedCMRCID, this.selectedStatus);
                        this.showMessage = true;
                        this.message = this._commonMessage.UpdateMsg;
                        setInterval(() => {
                            this.showMessage = false;
                        }, 5000);
                    }
                }
                else {
                    this._globalURL.isLoading = false;
                    this.showErrorMessage = true;
                    this.message = this._commonMessage.WarningMsg;
                    setInterval(() => {
                        this.showErrorMessage = false;
                    }, 5000);
                }
            }).catch((res) => {
                  this._globalURL.isLoading =false;
                  this.showErrorMessage = true;
                  this.message = this._commonMessage.CatchBlockMsg;
                  setInterval(() => {
                      this.showErrorMessage = false;
                  },5000)
            })

    }
    reRenderDT() {
        this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
            dtInstance.destroy();
            this.dtTrigger.next();
        });
    }


}


