import { Component } from "@angular/core";
import { DISTRICTService } from "../../../services/district.services";
import { GlobalAssets } from "../../../environments/Global";
import { CommonService } from "../../../services/common.service";
import { AccessToken } from "../../../environments/access_token";
import { CommonMessageComponent } from "../../../environments/CommanMssage";
import { Router } from "@angular/router";
import { PerentageIncome } from "./percentageIncome.model";

@Component({
  templateUrl: 'PercentageIncome.Component.html',
  styleUrls: ['../../../components/app.css']
})

export class PercentageOnIncome {
  public data1: any;
  public percentageUpdateModel: any;
  public showWarningMessage: boolean;
  public getPercentageDetails: any;
  public showErrorMessage: boolean;
  public message: any;
  public showMessage: boolean;
  constructor(public _districtService: DISTRICTService,
    public _globalURL: GlobalAssets,
    public _commonService: CommonService,
    public _access_token: AccessToken,
    public _router: Router,
    public _perincomeModel:PerentageIncome,
    public _common_message: CommonMessageComponent) {

  }

  ngOnInit(){
    this.getPercentage();
  }

  getPercentage(): void {
    this._globalURL.isLoading = true;
    debugger;
    this._districtService.get(this._globalURL.GET_PERCENTAGE_ON_INCOME, sessionStorage.getItem("access_token"))
        .then((res) => {
            if (res.Status == true) {
                this.getPercentageDetails = res.Data;
                this._globalURL.isLoading = false;
            }
            else {
                this.showWarningMessage = true;
                this.message = this._common_message.WarningMsg;
                setInterval(() => {
                    this.showWarningMessage = false;
                }, 5000);
                this._globalURL.isLoading = false;
            }
        }).catch((err) => {
            this._globalURL.isLoading = false;
            this.message = this._common_message.CatchBlockMsg;
            this.showErrorMessage = true;
            setInterval(() => {
                this.showErrorMessage = false;
            }, 5000);
        })
}

  CreateNewRecord(data: any): void {
    debugger;
    this._globalURL.isLoading = true;
    this._districtService.post(this._globalURL.Add_PERCENTAGE_ON_INCOME, data, sessionStorage.getItem("access_token"))
      .then((res) => {
        if (res.Status == undefined) {
          if (res.status == 200) {
            let result = JSON.parse(res._body);
            if (result.Status == true) {
              this._globalURL.isLoading = false;
              this.showMessage = true;
              this.message = this._common_message.PercentageOnIncomeMsg;
              this.getPercentage();
              setInterval(() => {
                this.showMessage = false;
              }, 5000);
            }
            this._globalURL.isLoading = false;
            setInterval(() => {
            }, 5000);
          }
          else {
            this._globalURL.isLoading = false;
            this.showErrorMessage = true;
            this.message = this._common_message.ErrorPercentageOnIncomeMsg;
            setInterval(() => {
                this.showErrorMessage = false;
            }, 5000);
          }
        }
        else {
          if (res.Status == 200) {
            this._globalURL.isLoading = false;
            this.showErrorMessage = true;
            this.message = this._common_message.ErrorPercentageOnIncomeMsg;
            setInterval(() => {
              this.showErrorMessage = false;
            }, 5000);
          }
          else {
            this._globalURL.isLoading = false;
            this.showErrorMessage = true;
            this.message = this._common_message.ErrorPercentageOnIncomeMsg;
            setInterval(() => {
              this.showErrorMessage = false;
            }, 5000);
          }
        }
      })
}

setUpdatedValues(data: any) {
  debugger;
  this._perincomeModel = data;
}

updatePercentageOnRole() {
  debugger;
  this._districtService.post(this._globalURL.UPDATE_PERCENTAGE_ON_INCOME, this._perincomeModel, sessionStorage.getItem("access_token"))
      .then((res) => {
          if (res.status === 401) {
              sessionStorage.clear();
              this.showErrorMessage = true;
              this.message = this._common_message.error401message;
              setInterval(() => {
                  this.showErrorMessage = false;
              }, 5000);
              this._globalURL.isLoading = false;
              this._router.navigate(['../login']);
          }
          if (res.Status == undefined) {
              var data = JSON.parse(res._body);
              if (data.Status == true) {
                  this._globalURL.isLoading = false;
                  this.showMessage = true;
                  this.message = this._common_message.UpdatePercentageOnIncomeMsg;
                  setInterval(() => {
                      this.showMessage = false;
                  }, 5000);
                  this.getPercentage();
              }
              else {
                  this.showWarningMessage = true;
                  this.message = this._common_message.WarningMsg;
                  setInterval(() => {
                      this.showWarningMessage = false;
                  }, 5000);
                  this._globalURL.isLoading = false;
              }
          }
          else {
              if (res.Status == true) {

              }
              else {
                  this.showWarningMessage = true;
                  this.message = this._common_message.WarningMsg;
                  setInterval(() => {
                      this.showWarningMessage = false;
                  }, 5000);
                  this._globalURL.isLoading = false;
              }
          }
      }).catch((err) => {
          this._globalURL.isLoading = false;
          this.message = this._common_message.CatchBlockMsg;
          this.showErrorMessage = true;
          setInterval(() => {
              this.showErrorMessage = false;
          }, 5000);
      })
}

}



