import { Component, OnInit, ViewChild, AfterViewInit } from '@angular/core';
import { constructor } from 'events';
import { Router } from '@angular/router';
import { PublicInfoModel } from './publicinfo.model';
import { NgForm } from "@angular/forms/src/forms";
import { CMRCService } from '../../../../services/cmrc.services';
import { GlobalAssets } from '../../../../environments/Global';
import { AccessToken } from '../../../../environments/access_token';
import { GlobalVariable } from '../../../../environments/GlobalVeriables';
import { FileUploadService } from '../../../../services/FileService/file_upload.service';
import { VOService } from '../../../../services/vo.services';
import { CommonMessageComponent } from '../../../../environments/CommanMssage';

@Component({
    templateUrl: './public-info-update.component.html',
    styleUrls: ['../../../../components/app.css']
})

export class PublicInfoComponent {
    public RefId: any;
    public showWarningMessage: boolean;
    public showErrorMessage: boolean;
    public InformationTypeId: string = null;
    public Title: string = null;
    public Id: any = null;
    public Information: string = null;
    public showSucessMessage: boolean;
    public ShowErrormsg: boolean;
    public commonMsg: any;
    public publicinformation: PublicInfoModel;
    public message: any;
    public showMessage: boolean;
    constructor(public _cmrcservice: CMRCService,
        public _router: Router,
        public _globalURL: GlobalAssets,
        public _access_token: AccessToken,
        public globalVariable: GlobalVariable,
        private _fileUploadService: FileUploadService,
        public _voService: VOService,
        public _common_message: CommonMessageComponent) {
        this.Information = sessionStorage.getItem("publicpageinfo");
        this.Title = sessionStorage.getItem("publicpageTitle");
        this.GetUserName();
        // this.InformationTypeId = this.globalVariable.publicInformationTypeId;
    }
    // ngOnInit() {
    //     this.getPUBLICinfo();

    // }
    debugger;
    SavePublicInfo(Publicinfo: any) {
        debugger;
        Publicinfo.Id = sessionStorage.getItem("PublicInfoId");
        Publicinfo.InformationTypeId = sessionStorage.getItem("publicInformationTypeId");
        this._globalURL.isLoading = true;
        this._cmrcservice.post(this._globalURL.ADD_PUBLIC_INFORMATION, Publicinfo, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.status == 200) {
                    // this.Information.data;
                    this._globalURL.isLoading = false;
                    this.message = " Public Information Updated Successfully";
                    this._router.navigate(['/cmrc/publicdetails']);
                    this.showSucessMessage = true;
                    setInterval((res) => {
                        this.showSucessMessage = false;
                    }, 5000)
                    this._globalURL.isLoading = false;
                }
                else {
                    this._globalURL.isLoading = false;
                    this.showSucessMessage = true;
                    this.message = this._common_message.ErrorMessage;
                    setInterval(() => {
                        this.showSucessMessage = false;
                    }, 5000);
                }
            }).catch((res) => {
                this._globalURL.isLoading = false;
                this.ShowErrormsg = true;
                this.message = this._common_message.CatchBolockMsg;
                setInterval(() => {
                    this.ShowErrormsg = false;
                }, 5000);
            });
    }
    public Publicinfo: NgForm;
    GetUserName() {
        this._voService.get(this._globalURL.GETUSERNAME, sessionStorage.getItem("access_token")).
            then((res) => {
                if (res.Status == true) {
                    this.RefId = res.data;

                    // this.AddPhotos(metadata:any);
                }
            }).catch((res) => {
                this.showErrorMessage = true;

                console.log(res.error);
                this.message = this._common_message.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this._globalURL.isLoading = false;
            })
    }
    AddPhotos(metadata: any) {
        debugger;

        var formData = new FormData();
        formData.append("RefId", sessionStorage.getItem("UserId"));
        formData.append("file", metadata);
        this._fileUploadService.post((this._globalURL.ADD_CMRC_PHOTO + "?folderName=CMRC_Photos" + "&type=" + this._globalURL.CMRC_PHOTO), formData, sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == undefined) {
                    if (res.status == 200) {
                        //  this.memberform.control.reset();
                        this.showMessage = true;
                        this.message = this._common_message.MemberCreatedMsg;
                        setInterval(() => {
                            this.showMessage = false;
                        }, 5000);
                    }
                }
                else {
                    this.showWarningMessage = true;
                    this.message = this._common_message.WarningMsg;
                    setInterval(() => {
                        this.showWarningMessage = false;
                    }, 5000);
                    this._globalURL.isLoading = false;
                }
            }).catch((res) => {
                this.showErrorMessage = true;
                console.log(res.error);
                this.message = this._common_message.CatchBlockMsg;
                setInterval(() => {
                    this.showErrorMessage = false;
                }, 5000);
                this._globalURL.isLoading = false;
            })
    }
}



