import { Injectable } from "@angular/core";

@Injectable()
export class CmrcDetailsModel {

    public Id: number;
    public CMRCName: string;  
    public AddressLine1: any;
    public DistrictUserId: number;
    public DistrictUserName: string;
    public FormationDate: any;
    public TalukaId: number; 
    public Taluka: any;
    public BankAccountNumber:any;
    public BankAddress: any;
    public BankName: any;
    public IFSCCode: any;
    public BranchName: any;
    public AreaId: any;

    constructor() {
        this.Id = 0;
        this.CMRCName = '';
        this.FormationDate = '';
        this.AddressLine1 = '';
        this.DistrictUserId = 0;
        this.DistrictUserName = '';
        this.TalukaId = 0;
        this.Taluka = '';
        this.BankAccountNumber = '';
        this.BankAddress = '';
        this.BankName = '';
        this.IFSCCode = '';
        this.BranchName = '';
        this.AreaId = '';
    }

} 





