import { Component, OnInit } from '@angular/core';
import { ExportToPDF } from '../../../../services/reports/exportpdf';
import { GlobalAssets } from '../../../../environments/Global';
import { GlobalVariable } from '../../../../environments/GlobalVeriables';
import { AccessToken } from '../../../../environments/access_token';
import { Router } from '@angular/router';
import { CMRCService } from '../../../../services/cmrc.services';
import { ThrowStmt } from '@angular/compiler';

@Component({
    templateUrl: 'loantakenmemornot.html'
})


export class LoanTakenOrNotMemberComponent {
  public  nonTakenMember: boolean;
  public  takenMember: boolean;
    public loanTakenMembers: any;
    public status: number;
    public frDate: any;
    public toDt: any;
    constructor(public _cmrcservice: CMRCService,
        public _router: Router,
        public _access_token: AccessToken,
        public globalVariable: GlobalVariable,
        public _globalURL: GlobalAssets,
        public exportToPDF: ExportToPDF) {            
        if (sessionStorage.getItem("access_token") == "") {
            _router.navigate(['../../login']);
        }
    }

    ngOnInit() {
        this.status = 1;
        this.getLoanTakenOrNotTakenMemberDetails(this.status);
    }

    getLoanTakenOrNotTakenMemberDetails(status: number) {
        this.nonTakenMember = false;
        this.takenMember = false;
        debugger;       
        this.status = status;
        this._globalURL.isLoading = true;
        this._cmrcservice.get((this._globalURL.MEMBER_LOAN_TAKEN_OR_NOT_TAKEN + status + "&frDt=null" + "&toDt=null"), sessionStorage.getItem("access_token"))
            .then((res) => {
                if (res.Status == true) {
                    this.loanTakenMembers = res.Data;
                    status == 1? this.takenMember= true : this.nonTakenMember = true;
                    this._globalURL.isLoading = false;
                }
                else {
                    this._globalURL.isLoading = false;
                }
            });
    }

    public loanTakenMemberPdf(){
        debugger;
        this._globalURL.isLoading = true;
       this.takenMember ? this.exportToPDF.loanTakenMemberExportToPdf(this.loanTakenMembers) : this.exportToPDF.loanNotTakenMemberExportToPdf(this.loanTakenMembers)
       this._globalURL.isLoading = false;
    }
}




